

































import {
  defineComponent, ref, reactive,
} from '@vue/composition-api';
// import { getDeclineRecord } from '@/loaders/material-mangement';
import axios from '@/utils/axios';
import useLoader from '_/composables/use-loader';

async function getDeclineRecord(id: string) {
  return (await axios({
    method: 'GET',
    url: '/anta-material/audit/decline',
    params: {
      materialId: id,
    },
  })).data;
}
async function getUserInfo(ids: string[]) {
  return (await axios({
    method: 'POST',
    url: '/authcenter/user/im/info',
    data: ids,
  })).data;
}

export default defineComponent({
  name: 'DeclineRecord',
  dialogify: {
    props: {
      // title: '驳回原因',
      width: '540px',
      // closeOnClickModal: false,
      showClose: true,
      top: 'auto',
      bottom: 'auto',
      flex: 'column',
      'custom-class': 'decline-record-dialog',
    },
  },
  components: {
  },
  props: {
    materialId: {
      type: String,
      required: true,
    },
  },
  setup(props, { emit }) {
    const declineRecord = useLoader(async () => {
      const records = await getDeclineRecord(props.materialId);
      const userIds = Array.from(new Set(records.map(item => item.creator)));
      if (userIds.length > 0) {
        const userInfos = await getUserInfo(userIds);
        records.forEach(rec => {
          rec.creator = userInfos.find(user => user.id === rec.creator)?.name;
        });
        return records;
      }
      return [];
    });
    return {
      declineRecord,
    };
  },
});
