

























// import i18n from 'i18next';
import { defineComponent, computed } from '@vue/composition-api';

import UnitField from './UnitField.vue';

interface Unit {
  type: string,
  value: number | string,
}

export default defineComponent({
  name: 'MaterialFormFieldText',
  components: { UnitField },
  props: {
    // 标签名称
    label: {
      type: String,
      required: true,
    },
    // 内容
    content: {
      type: [String, Number, Array],
      default: '',
    },
    // 单位数组
    units: {
      type: Array as () => Unit[],
      default: () => [],
    },
  },
  setup(props) {
    const showPlaceHolder = computed(() => {
      if (props.units[0]?.type === 'priceUnit') {
        return !props.content;
      }
      return !props.content && props.units.length === 0;
    });
    return {
      showPlaceHolder,
    };
  },
});
