



















































































import {
  defineComponent, ref, onMounted, provide, watch, computed, onBeforeUnmount, onUnmounted,
} from '@vue/composition-api';

import axios from '@/utils/axios';
import set from '@/utils/key-serializer';
import isQt from '@/utils/is-qt';

import MaterialTypeCascade from '@/components/MaterialTypeCascade.vue';
import { AntaMaterialInfo } from '@/models/AntaMaterialInfo';
import { fetchAntaPropertyByCode } from '@/loaders/dictionary';
import validateForm from '@/utils/validators/validate-form';

import { getCurrentCompany } from '@/loaders/company';

import AntaMiddleClass2Form from '@/components/dynamic-form/AntaMiddleClass2Form';

import DictionarySelectorNew from '@/components/dictionary/DictionarySelectorNew.vue';
// import DictionarySelectorAnta from '@/components/dictionary/DictionarySelectorAnta.vue';
import { getHost, exec } from '@/utils/exec';
import { ElForm } from 'element-ui/types/form';
// import ReferencePrice from './FormComponents/ReferencePrice';
import SeasonSelect from './FormComponents/SeasonSelect/index.vue';
import rules from './detail-validate';

/**
 * 获取材质详情信息
 * @param {String} materialId 材质Id
 * @see http://yapi.4dshoetech.local/project/115/interface/api/6026
 */
export const getMaterialInfo = (materialId: string, language = 'cn') => {
  return axios({
    method: 'get',
    url: '/bff-center/showCommon/getMaterialBasicInfoAndTags',
    headers: {
      language,
    },
    params: { materialId },
  });
};

// 类目详情
export const fetchMaterialDetail = async (id: string) => {
  return (await axios({
    method: 'get',
    url: `/anta-material/category/detail?id=${id}`,
  })).data;
};

// 类目详情
export const fetchDefaultUnitId = async (property: string) => {
  return (await axios({
    method: 'get',
    url: '/anta-material/system/default/unit/category',
    params: {
      property,
    },
  })).data;
};

// 修改面料详情新增操作记录
export const addOperate = (groupAppId: string, data: { relateId: string, params: any }) => {
  return axios({
    headers: {
      groupAppId,
    },
    method: 'post',
    url: '/msgcenter/operate/add',
    data,
  });
};

// 获取全部供应商
export async function fetchSupplierAll() {
  const res = (await axios({
    method: 'post',
    url: '/anta-material/supplier/list/condition',
    data: {},
  })).data;
  return res;
}

export default defineComponent({
  name: 'AntaMaterialForm',
  components: {
    // DictionarySelectorNew,
    // DictionarySelectorAnta,
    MaterialTypeCascade,
    // ReferencePrice,
    // SeasonSelect,
    AntaMiddleClass2Form,
  },
  props: {
    material: {
      type: Object,
      default: null,
    },
    status: {
      type: String as () => 'upload' | 'edit' | 'convert',
      default: 'edit',
    },
    templateMaterialId: {
      type: String,
      default: '',
    },
    isCreator: {
      type: Boolean,
      default: false,
    },
  },

  setup(props, { root }) {
    const host = ref(null);

    const isSupplier = computed(() => {
      return getCurrentCompany()?.type === 3;
    });

    // 表单ref
    const templateForm = ref<InstanceType<typeof AntaMiddleClass2Form>>();
    const refsForm = ref<InstanceType<typeof ElForm>>();
    const typeCascade = ref<InstanceType<typeof MaterialTypeCascade>>();

    // 自动表单数据
    const propertyFromTemplate = ref<Partial<AntaMaterialInfo['extraProperty'] & { materialName: string }>>({});

    // 是否创建者
    // const isCreator = ref(false);

    const canEdit = computed(() => {
      return props.status === 'upload' || props.status === 'convert' || (props.status === 'edit' && props.isCreator);
    });

    provide('getStatus', props.status);

    // 用于保存变更记录
    const updateData = ref<{ [key: string]: any }[]>([{}, {}]);

    // 获取全部供应商列表
    const supplierList = ref<{ [key: string]: { companyId?: string, id: string, name: string, originalSupplierName: string } }>({});

    // 用于对比哪些属性发生改变
    const oldMaterial = ref<Partial<AntaMaterialInfo>>({});
    const newMaterial = ref<AntaMaterialInfo>({
      materialName: '',
      extraProperty: {
        category: '',
        evergreen: 2,
      },
    });

    // 获取分类信息
    const parentIds = ref<string[]>([]);

    // 中类ID
    const middleCategoryId = computed(() => parentIds.value[1]);

    // 类别节点数组
    const categoryArr = ref<any[]>([]);

    watch(() => categoryArr.value, async (v: any[]) => {
      if ((props.status === 'upload' || props.status === 'convert')) {
        const unit = await fetchDefaultUnitId(v[2].name);
        if (!props.templateMaterialId) {
          const companyId = await getCurrentCompany()?.id || '';
          root.$set(propertyFromTemplate.value, '单位', unit);
          root.$set(propertyFromTemplate.value, 'supplier_name', Object.values(supplierList.value).find((s) => s.companyId === companyId)?.id || '');
          root.$set(propertyFromTemplate.value, 'reference_price', { value: null, unit: 1 });
          root.$set(propertyFromTemplate.value, '品牌编码', (await fetchAntaPropertyByCode('品牌编码'))[0].id);
        }
      }
    });

    watch(() => templateForm.value?.jsonschema, async (val) => {
      if (val) {
        await root.$nextTick();
        const form = refsForm.value?.$el as unknown as HTMLFormElement;

        if (host.value) {
          await exec('recvWebSizeChanged', {
            width: form.offsetWidth,
            height: form.offsetHeight,
          });
        }
      }
    });

    // 处理jsonschema
    function setJsonschemaFormItem(jss: any[]) {
      jss.find(i => i.prop === 'materialName').maxLength = 512;
      jss.find(i => i.prop === 'supplier_name').filterable = true;
      if (props.status === 'edit') {
        jss.find(i => i.title === '品牌编码').disabled = true;
        jss.find(i => i.prop === 'season').disabled = true;
        if (!props.material.extraProperty.season) {
          jss.find(i => i.prop === 'season').required = false;
        }
        if (jss.find(i => i.prop === 'supplier_name')) {
          jss.find(i => i.prop === 'supplier_name').disabled = true;
        }
      } else {
        const materialNumberIndex = jss.findIndex(i => i.prop === 'material_number');
        if (materialNumberIndex !== -1) {
          jss.splice(materialNumberIndex, 1);
        }
      }

      const categoryIndex = jss.findIndex(i => i.prop === 'category');
      if (categoryIndex !== -1) {
        jss.splice(categoryIndex, 1);
      }
      const costIndex = jss.findIndex(i => i.prop === 'cost');
      if (costIndex !== -1) {
        jss.splice(costIndex, 1);
      }
      if (isSupplier.value) {
        const evergreenIndex = jss.findIndex(i => i.prop === 'evergreen');
        if (evergreenIndex !== -1) {
          jss.splice(evergreenIndex, 1);
        }
        if (jss.find(i => i.prop === 'supplier_name')) {
          jss.find(i => i.prop === 'supplier_name').disabled = true;
        }
      }
      return jss;
    }

    // 提交变更记录（外部调用）
    async function addOperateToRecord(groupAppId = '') {
      if (!groupAppId) return;

      // 统计变更记录数量
      let count = 0 as number;


      const _propertyFromTemplate = JSON.parse(JSON.stringify(propertyFromTemplate.value));
      const firstSupplier = {
        reference_price_unit: _propertyFromTemplate.reference_price.unit,
        reference_price_value: _propertyFromTemplate.reference_price.value,
        cost_unit: 1,
        cost_value: '',
        supplier_name: _propertyFromTemplate.supplier_name,
        delete_status: 0,
        supplier_status: 1,
      };

      let _newMaterial = {
        ...newMaterial.value,
        materialName: _propertyFromTemplate.materialName,
      } as Partial<AntaMaterialInfo>;

      const omitList = ['materialName', 'reference_price', 'cost', 'supplier_name'];
      omitList.forEach((i) => {
        if (Object.hasOwnProperty.call(_propertyFromTemplate, i)) {
          delete _propertyFromTemplate[i];
        }
      });

      _newMaterial = {
        ..._newMaterial,
        extraProperty: {
          ...newMaterial.value.extraProperty,
          ..._propertyFromTemplate,
          supplier_price_array: [
            firstSupplier,
            ...newMaterial.value.extraProperty.supplier_price_array!.slice(1),
          ],
        },
      };
      Object.keys(_newMaterial.extraProperty!).forEach((_p) => {
        const p = _p as keyof AntaMaterialInfo['extraProperty'];
        if ([
          'material_status',
          'cite_status',
          'decline_reason',
          'supplier_price_obj',
          'decline_time',
          'approve_detail',
          'season',
        ].find(i => i === p)) {
          return;
        }

        if (p === 'supplier_price_array') {
          _newMaterial.extraProperty!.supplier_price_array!.forEach((priceItem, i) => {
            let diffPriceCount = 0;
            Object.keys(priceItem).forEach(key => {
              const oldPriceItem = oldMaterial.value.extraProperty!.supplier_price_array!;
              if (priceItem[key as keyof typeof priceItem] !== oldPriceItem[i][key as keyof typeof priceItem]) {
                diffPriceCount += 1;
              }
            });
            if (diffPriceCount > 0) {
              updateData.value[0][p] = _newMaterial.extraProperty![p];
              updateData.value[1][p] = oldMaterial.value.extraProperty?.[p];
              count += 1;
            }
          });
        } else if (JSON.stringify(_newMaterial.extraProperty![p]) !== JSON.stringify(oldMaterial.value.extraProperty?.[p])
          && (!!_newMaterial.extraProperty![p] || !!oldMaterial.value.extraProperty?.[p])) {
          updateData.value[0][p] = _newMaterial.extraProperty![p];
          updateData.value[1][p] = oldMaterial.value.extraProperty?.[p];
          count += 1;
        }
      });

      if (_newMaterial.materialName === oldMaterial.value.materialName && count === 0) {
        return;
      }

      updateData.value[0].materialName = _newMaterial.materialName;
      updateData.value[1].materialName = oldMaterial.value.materialName;

      const relateId = _newMaterial.originalId;
      if (relateId) {
        await addOperate(groupAppId, {
          relateId,
          params: updateData.value,
        });
      }
    }

    // 修改鞋模型信息
    function updateInfo(prop: string, value: unknown | number | string) {
      if (typeof value === 'string') {
        value = value.replace(/（/g, '(').replace(/）/g, ')');
      }
      if (prop === 'materialName') {
        // set(updateObj.value, prop, value);
        set(newMaterial.value, prop, value);
      } else {
        // 分类
        // set(updateObj.value.extraProperty, prop, value);
        set(newMaterial.value.extraProperty, prop, value);
      }
    }

    // 检测表单是否通过校验,通过后返回表单数据（外部调用）
    async function getFormData() {
      await Promise.all([validateForm(refsForm.value!), templateForm.value?.validate()]);

      let formMatarialData = {
        materialName: propertyFromTemplate.value.materialName,
      } as Partial<AntaMaterialInfo>;

      const _propertyFromTemplate = JSON.parse(JSON.stringify(propertyFromTemplate.value));

      const firstSupplier = {
        reference_price_unit: _propertyFromTemplate.reference_price.unit,
        reference_price_value: _propertyFromTemplate.reference_price.value,
        cost_unit: 1,
        cost_value: '',
        supplier_name: _propertyFromTemplate.supplier_name,
        delete_status: 0,
        supplier_status: 1,
      };

      const omitList = ['materialName', 'reference_price', 'cost'];
      omitList.forEach((i) => {
        if (Object.hasOwnProperty.call(_propertyFromTemplate, i)) {
          delete _propertyFromTemplate[i];
        }
      });


      if (props.status === 'upload' || props.status === 'convert') {
        // 获取大类中类名称
        let bigType = '';
        let midType = '';
        if (categoryArr.value.length) {
          bigType = categoryArr.value[0].name;
          midType = categoryArr.value[1].name;
        } else {
          const checkedNodes = typeCascade.value?.getCheckedNodes();
          bigType = checkedNodes[0].name;
          midType = checkedNodes[1].name;
        }
        formMatarialData = {
          ...formMatarialData,
          businessType: 1,
          extraProperty: {
            ...newMaterial.value.extraProperty,
            ..._propertyFromTemplate,
            category: newMaterial.value.extraProperty.category,
            supplier_price_array: [
              firstSupplier,
              // ...newMaterial.value.extraProperty.supplier_price_array.slice(1),
            ],
            大类: bigType,
            中类: midType,
            // material_number: materialNumber,
          },
        };
      } else {
        formMatarialData = {
          ...formMatarialData,
          businessType: 1,
          extraProperty: {
            ...newMaterial.value.extraProperty,
            ..._propertyFromTemplate,
            supplier_price_array: [
              firstSupplier,
              ...newMaterial.value.extraProperty.supplier_price_array!.slice(1),
            ],
          },
        };
      }

      if (props.isCreator && props.status === 'edit') {
        formMatarialData.extraProperty!.material_status = 1;
      }

      // 上传
      if (props.status === 'upload' && isQt()) {
        return {
          success: true,
          errorMsg: '',
          material: formMatarialData,
        };
      }

      if (props.templateMaterialId) {
        const imgJson = {} as { [key: string]: string };
        let renderingJson = {};
        let fileInfo = '';
        if (newMaterial.value.imgJson) {
          Object.keys(newMaterial.value.imgJson).forEach((i) => {
            imgJson[i] = newMaterial.value.imgJson![i].split('?')[0].split('/').splice(-2).join('/');
          });
        }
        if (newMaterial.value.fileInfo) {
          fileInfo = newMaterial.value.fileInfo.split('?')[0].split('/').splice(-2).join('/');
        }
        if (newMaterial.value.renderingJson) {
          renderingJson = newMaterial.value.renderingJson;
        }

        formMatarialData = {
          ...formMatarialData,
          imgJson,
          fileInfo,
          renderingJson,
        };
      }
      // debugger;

      console.info(formMatarialData);

      console.info(formMatarialData);

      return formMatarialData;
    }

    onMounted(async () => {
      // 上传
      if (props.status === 'upload' && isQt()) {
        host.value = await getHost();

        if (host.value && root.$route.path.includes('/qt/upload/anta-material')) {
          host.value!.on('requestAntaForm', async () => {
            let formData = {};
            if (templateForm.value?.notHasTemplate) {
              formData = {
                success: false,
                errorMsg: '对应的分类没有模板，请更换一个有模板的分类',
                material: null,
              };
            }
            try {
              formData = await getFormData();
            } catch (e) {
              formData = {
                success: false,
                errorMsg: '',
                material: null,
              };
            }

            await exec('recvAntaForm', formData);
          });
        }
      }
      window.onmousewheel = async (e) => {
        if (isQt() && root.$route.path.includes('/qt/upload/anta-material')) {
          if (e.target.className.includes('el-form-item') || e.target.className.includes('el-form')) {
            await exec('onWebScrollValue', e.deltaY);
          }
        }
      };

      // 获取供应商信息
      const temp = {} as { [x: string]: { id: string, name: string, originalSupplierName: string } };
      (await fetchSupplierAll()).forEach((sup: { id: string, name: string, originalSupplierName: string }) => {
        temp[`${sup.id}`] = sup;
      });
      supplierList.value = temp;

      // 上传（非以此为模版）或转换、供应商
      if (isSupplier.value && (props.status === 'upload' || props.status === 'convert') && !props.templateMaterialId) {
        // const companyId = await getCurrentCompany()?.id || '';

        // newMaterial.value.extraProperty.supplier_price_array[0].supplier_name = Object.values(supplierList.value)
        //   .find((s) => s.companyId === companyId)?.id || '';

        // propertyFromTemplate.value.supplier_name = Object.values(supplierList.value).find((s) => s.companyId === companyId)?.id || '';
        // root.$set(propertyFromTemplate.value, 'reference_price', { value: null, unit: 1 });
        // propertyFromTemplate.value.reference_price = { value: null, unit: 1 };

        // propertyFromTemplate.value.品牌编码 = (await fetchAntaPropertyByCode('品牌编码'))[0].id;
      }

      // 以此为模版上传
      if (props.templateMaterialId) {
        const templateMaterial = (
          await getMaterialInfo(props.templateMaterialId, 'cn')
        ).data;

        // $set(props, 'material', templateMaterial);
        newMaterial.value = templateMaterial;

        propertyFromTemplate.value = {
          ...JSON.parse(JSON.stringify(newMaterial.value.extraProperty)),
          materialName: newMaterial.value.materialName,
          supplier_name: newMaterial.value.extraProperty.supplier_price_array![0].supplier_name,
          reference_price: {
            value: newMaterial.value.extraProperty.supplier_price_array![0].reference_price_value,
            unit: newMaterial.value.extraProperty.supplier_price_array![0].reference_price_unit,
          },
          // cost: newMaterial.value.extraProperty.supplier_price_array![0].cost_value,
        };
      }

      // 编辑
      if (props.status === 'edit') {
        //
        if (props.material) {
          newMaterial.value = props.material as AntaMaterialInfo;
        }
        propertyFromTemplate.value = {
          ...JSON.parse(JSON.stringify(newMaterial.value.extraProperty)),
          materialName: newMaterial.value.materialName,
          supplier_name: newMaterial.value.extraProperty.supplier_price_array![0].supplier_name,
          reference_price: {
            value: newMaterial.value.extraProperty.supplier_price_array![0].reference_price_value,
            unit: newMaterial.value.extraProperty.supplier_price_array![0].reference_price_unit,
          },
          // cost: newMaterial.value.extraProperty.supplier_price_array![0].cost_value,
        };
        oldMaterial.value = JSON.parse(JSON.stringify(newMaterial.value)) as AntaMaterialInfo;
      }

      // 如果有分类id
      if (newMaterial.value.extraProperty.category) {
        const res = await fetchMaterialDetail(newMaterial.value.extraProperty.category);
        parentIds.value = [...res.parentIds.slice(), newMaterial.value.extraProperty.category];
      }
    });

    onUnmounted(() => {
      window.onmousewheel = null;
    });
    return {
      isQt,
      rules,
      addOperateToRecord,
      updateInfo,
      parentIds,
      getFormData,
      middleCategoryId,
      typeCascade,
      propertyFromTemplate,
      categoryArr,
      supplierList,
      refsForm,
      templateForm,
      newMaterial,
      canEdit,
      setJsonschemaFormItem,
      isSupplier,
    };
  },
});
