import AppError from '@/utils/error';
import { getHost } from '@/utils/exec';

type QtUrlOptions = { url: string, query?: Record<string, string| null | string[]>, params?: any };
export async function openQtWin(urlOrUrlOptions: string | QtUrlOptions) {
  const nativeHost = await getHost();
  return new Promise((resolve, reject) => {
    nativeHost.once('winClosed', (res: {data: any, message: string, success: boolean}) => {
      if (res.success) {
        resolve(res.data);
      } else {
        reject(new AppError(res));
      }
    });
    let urlOptions: QtUrlOptions;
    if (typeof urlOrUrlOptions === 'string') {
      urlOptions = { url: urlOrUrlOptions };
    } else {
      urlOptions = urlOrUrlOptions;
    }
    nativeHost.openWin(urlOptions);
  });
}
export default openQtWin;
