<script>

import Qty from 'js-quantities';
import i18n from 'i18next';


// console.log((new Qty('1yd')).kind());
// console.log(Qty.getUnits('area_density'));
// console.log(Qty.swiftConverter('kg/m^2', 'g/m^2')(1));

export default {
  name: 'DetailTextField',
  props: {
    // 标题
    label: {
      type: String,
      default: i18n.t('public.form.label_cap'),
    },
    // 内容 或者 数组的长度
    content: {
      type: [String, Number],
      default: '',
    },
    // 可切换的单位
    units: {
      type: Array,
      default: () => [],
    },
    // 内容的单位
    unit: {
      type: [String, undefined],
      default: '',
    },
    // 是否超过长度后隐藏多余的文本
    ellipsis: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      // 用户选择的单位
      innerUnit: this.unit,
    };
  },
  watch: {
    unit(u) {
      this.innerUnit = u;
    },
  },
  computed: {
    innerContent() {
      if (!this.units || !this.content) return this.content;
      if (this.innerUnit === this.unit) return this.content;
      return +Qty.swiftConverter(this.unit, this.innerUnit)(+this.content).toFixed(2);
    },
  },
  render() {
    const props = this;
    // 根据内容的值，渲染不同的内容
    const renderContent = () => {
      const placeholder = <p class="placeholder text-one-line">{ i18n.t('public.form.fill_in_later') }</p>;

      // 当传入为空字符 或 数组长度为0时，显示占位符文本
      if (!props.content && props.content !== 0) {
        return placeholder;
      }


      return this.$scopedSlots.default ? this.$scopedSlots.default()
        : <p class={props.ellipsis ? 'content text-one-line' : 'content'}>
          {props.innerContent}{' '}
          {
            // eslint-disable-next-line no-nested-ternary
            props.unit ? (props.units.length ? (<el-dropdown>
                  <span class="el-dropdown-link">
                    <el-tag size="mini" style="cursor: pointer">
                      {props.innerUnit}{' '}
                      <i class="el-icon-arrow-down"/>
                    </el-tag>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    { props.units.map(unit => <el-dropdown-item
                    nativeOnClick={() => { this.innerUnit = unit; }}>{unit}</el-dropdown-item>) }
                  </el-dropdown-menu>
                </el-dropdown>)
              : <el-tag size="mini" type="info">{props.unit}</el-tag>)
              : null
          }
        </p>;
    };

    return (
      <div class="detail-form-field text-one-line">
        <label class="label">{props.label}</label>
        {renderContent()}
      </div>
    );
  },
};
</script>

<style lang="scss" scoped>
.detail-form-field {
  margin-bottom: 15px;

  .label {
    font-size: 12px;
    font-weight: 400;
    line-height: 24px;
    color: #999;
  }
  .content,
  .placeholder {
    min-height: 22px;
    font-size: 16px;
    line-height: 22px;
    color: #333;
  }
  .placeholder {
    color: #999;
  }
}
</style>
