import axios from '@/utils/axios';

type Value = {
  id?:string,
  label: string
};

export type Property = {
  id: string,
  title: string,
  required: number,
  component: number,
  params: any,
  values: Value[] | ''
};

const cache: {
  properties: Property[]
} = {
  properties: [],
};

export async function fetchProperties() {
  if (cache.properties && cache.properties.length === 0) {
    cache.properties = (await axios({
      url: '/anta-material/dynamics/property/list',
      method: 'get',
    })).data;
  }
  return cache.properties;
}
export function getSelectionProperty(title: string) {
  return cache.properties.find(p => {
    return p.title === title && p.values && p.values.length !== 0;
  }) || null;
}

export function getProperty(title: string) {
  return cache.properties.find(p => {
    return p.title === title;
  }) || null;
}

export async function getPropertyByTitle(title: string) {
  if (cache.properties && cache.properties.length === 0) {
    try {
      await fetchProperties();
    } catch {
      cache.properties = [];
    }
  }
  return getProperty(title);
}

export function findValue(value: string | string[], property: Property | null, placeholder:string = '--') {
  if (!property) return value || placeholder;
  if (!value) return placeholder;
  const values = property.values as Value[];
  // component=3表示是数组，要从值列表中找出对应值，组成数组再拼接成字符串
  if (property.component === 3) {
    return values.filter(({ id }) => (value as string[]).includes(id))?.map(i => i.label)?.join(', ');
  }
  return values.find(v => {
    return v.id === value;
  })?.label || placeholder;
}

export default {};
