import { defineComponent } from '@vue/composition-api';
// import NumberType from '@4dst-saas/form-utils/dist/components/jsonschema2form/NumberType';
import NumberType from '@4dst-saas/form-utils/dist/components/jsonschema2form/NumberType';
import isFilled from '@/utils/validators/is-filled';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';

const PriceFormItem = defineComponent({
  // model: {
  //   prop: 'reference_price',
  //   event: 'change',
  // },
  props: {
    reference_price: {
      type: Object as () => {
        value: number, unit: number,
      },
      reuqired: true,
      default: () => ({
        value: null,
        unit: 1,
      }),
    },
  },

  render() {
    // const { $attrs: attrs } = this;
    return <div class="flex" style="width: 100%;" >
      <NumberType {...this.$attrs.attrs}
        val={ this.reference_price.value }
        rules={ this.$attrs.required ? validator2ElemRule(isFilled) : null}
        onchange={(e: number) => {
          this.reference_price.value = e;
          // this.$set(this.reference_price, 'value', e);
          // console.info(this.reference_price);
          // this.$emit('change', this.reference_price);
        }}
      ></NumberType>
      <el-select
        disabled={this.$attrs.disabled}
        size="small"
        style="width: 124px; margin-left: 6px"
        vModel={ this.reference_price.unit }
        // value={ this.reference_price.unit }
        // onchange={(e: number) => {
        //   this.reference_price.unit = e;
        //   this.$set(this.reference_price, 'unit', e);
        //   console.info(this.reference_price);
        //   // thi-s.$emit('change', this.reference_price);
        // }}
        >
        <el-option value={1} label='人民币'></el-option>
        <el-option value={2} label='美元'></el-option>
      </el-select>
    </div>;
  },
});

export default PriceFormItem;
