
import { computed, defineComponent } from '@vue/composition-api';
import { stop } from '_/jsx-utils';
import Catalog from '@/models/Catalog';
import { appType2Icon, appType2Name } from './app-type-to-name';

function rpx(size: number, ratio: number = 1) {
  return `${(size * ratio)}px`;
  // return `${($size / $baseFontSize)}em`;
}
const ItemFolder = defineComponent({
  props: {
    item: {
      required: true,
      type: Object as () => Catalog,
    },
    itemSize: {
      default() {
        return {
          defaultWidth: 220,
          scale: 1,
        };
      },
      type: Object,
    },
  },
  setup(p, { emit }) {
    const elemStyles = computed(() => {
      const { itemSize } = p;
      return {
        img: {
          height: rpx(194, itemSize.scale),
          width: rpx(itemSize.defaultWidth, itemSize.scale),
        },
        info: {
          width: rpx(220, itemSize.scale),
        },
      };
    });
    const icon = computed(() => {
      const { appType } = p.item;
      if (appType && (appType in appType2Icon)) {
        return appType2Icon[appType];
      }
      return undefined;
    });
    const name = computed(() => {
      const { appType, name: _name } = p.item;
      if (appType && (appType in appType2Name)) {
        return appType2Name[appType];
      }
      return _name;
    });
    function handleDblClick() {
      emit('enterFolder', p.item);
    }
    function handleRename() {
      emit('rename', p.item);
    }
    return {
      elemStyles, icon, name, handleDblClick, handleRename,
    };
  },
  render() {
    const self = this as InstanceType<typeof ItemFolder>;
    const {
      $listeners, item, elemStyles, icon, name, handleRename, handleDblClick,
    } = self;
    const renameable = !!$listeners.rename;
    return (
      <div
        class={['panel-item-folder', { renameable }]}
        onDblclick={handleDblClick}>
        <div
          class={['icon-folder', {
            'is-filled': item.haveChild,
            'is-recommend': item.isRecommend,
          }]}
          style={elemStyles.img}>
          {icon ? <i class={icon} /> : null}
          {item.isRecommend ? <span class="recommend-tag">
            <i class="iconfont icontuijian2" />
            <font>已推荐</font>
          </span> : null}
        </div>
        {renameable
          ? <div class="name" style={elemStyles.info} title={name} onDblclick={stop.prevent()}
            onClick={handleRename}><span>{name}</span></div>
          : <div class="name" style={elemStyles.info} title={name}><span>{name}</span></div>}

      </div>
    );
  },
});
export default ItemFolder;
