import { fetchAntaPropertyByCode } from '@/loaders/dictionary';

export type Obj = {
  [key in string | number]: any;
};

export interface ObjValue{
  year: string,
  season: 'Q1'|'Q2'|'Q3'|'Q4'|'',
  品牌: string
}
export type Value = ObjValue | null;

export interface Option{
  id: string,
  value:string,
}

export type Options = Option[];

export const _map:Obj = {
  0: 'year',
  1: 'season',
  2: '品牌',
};


const getYears = () => {
  const currentYear = 2020;
  // const range = [-20, 50];
  // const sortNums = [...Array(range[1] - range[0])].map((_, i) => range[0] + i).sort((a, b) => Math.abs(a) - Math.abs(b));
  const afterYears = [...Array(51)].map((_, i) => currentYear + i);
  const beforeYears = [...Array(20)].map((_, i) => currentYear - i - 1);
  const years = [...afterYears, ...beforeYears].map(item => {
    const value = `${item}`;
    return {
      id: value,
      label: value,
    };
  });
  return years;
};

const getSeansons = () => [...Array(4)].map((_, i) => ({ id: `Q${i + 1}`, label: `Q${i + 1}` }));

const combine = (chunks:Obj[][]) => {
  const result:any[] = [];

  const helper = (i:number, prev:Obj[] = []) => {
    if (i === chunks.length) {
      result.push(prev);
      return;
    }

    // eslint-disable-next-line no-restricted-syntax
    for (const item of chunks[i]) {
      helper(i + 1, [...prev, item]);
    }
  };

  helper(0);

  return result;
};

export const getCombines = async () => {
  const productTypes = await fetchAntaPropertyByCode('品牌');
  const years = getYears();
  const seasons = getSeansons();
  let combines = combine([years, seasons, productTypes]);


  combines = combines.map(_combine => {
    const value = _combine.reduce((obj:Obj, cur:Obj, index:number) => {
      const key = _map[index];
      obj[key] = cur.id;
      return obj;
    }, {});

    const id = JSON.stringify(value);
    const label = _combine.map((item:Obj, i:number) => (_map[i] === 'year' ? +item.label.slice(2) : item.label)).join('');

    return {
      id,
      value: label,
    };
  });

  return combines;
};
