// 根据中类id获取模版信息
export const getTemplateById = async (id: string) => {
  return (await axios({
    method: 'get',
    url: 'anta-material/template/middle',
    params: {
      id,
    },
  })).data;
};

interface Unit {
  type: string,
  value: number | string,
}

interface FormField {
  label: string,
  value?: string,
  units?: Array<Unit>,
}

export default defineComponent({
  name: 'MaterialDetailText',
  components: { FormFielText },
  props: {
    material: {
      type: Object,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
  },
  setup(props, { root, emit }) {
    const loading = ref(true);
    const fields = ref<Array<FormField>>([]);
    const attachFields = ref<Array<FormField>>([]);
    const isCreator = computed(() => getContextUser()?.id === props.material.creator);

    const isSupplier = computed(() => {
      return getCurrentCompany()?.type === 3;
    });

    const middleCategoryId = ref<string>('');

    const categoryName = ref<string>('');

    const supplierName = ref<string>('');

    watch(() => props.material, async (val) => {
      console.info(val);
      loading.value = true;

      // let colorLimitType = '';
      // switch (val.extraProperty.color_limit_type) {
      //   case 1: colorLimitType = '全部颜色';
      //     break;
      //   case 2: colorLimitType = '仅部分颜色';
      //     break;
      //   case 3: colorLimitType = '荧光色除外';
      //     break;
      //   default:
      // }

      const priceItem = val.extraProperty.supplier_price_obj ?? val.extraProperty.supplier_price_array[0];

      fields.value = [
        // { label: '分类', value: categoryName.value },
        // {
        //   label: '季节',
        //   value: `${val.extraProperty.season?.year || ''}${val.extraProperty.season?.season || ''}`,
        // },
        // { label: '供应商名称', value: priceItem.supplier_name },
        // {
        //   label: '参考单价',
        //   value: priceItem.reference_price_value ? `${priceItem.reference_price_value}人民币` : '--',
        // },
        // {
        //   label: '成本单价',
        //   value: priceItem.cost_value ? `${priceItem.cost_value}人民币` : '--',
        // },
        // { label: '颜色限制类别（鞋）', value: colorLimitType },
        // { label: '材料编码', value: val.extraProperty.material_number },
      ];

      const extraList = [];
      if (isSupplier.value) {
        extraList.push('常青材料');
      }

      // 可能会报错的接口放到后面去请求
      try {
        // 获取中类ID
        middleCategoryId.value = await fetchMiddleCategoryId(val.extraProperty.category);

        const dynamicFormData = await antaMiddleClassIdAndFormData2TransformedFormDataList(middleCategoryId.value, {
          ...val.extraProperty,
          reference_price: {
            value: priceItem.reference_price_value,
            unit: priceItem.reference_price_unit,
          },
          cost: {
            value: priceItem.cost_value,
            unit: priceItem.cost_unit,
          },
          supplier_name: priceItem.supplier_name,
          materialName: val.materialName,
        }, extraList);
        console.info(dynamicFormData);
        fields.value = [...fields.value, ...dynamicFormData];

        emit('loaded', props.material);
      } catch (e) {
        root.$errorHandler(e);
      } finally {
        loading.value = false;
      }
    }, {
      deep: true,
      // immediate:
    });

    return {
      loading,
      fields,
      attachFields,
      isCreator,
      categoryName,
      supplierName,
      isSupplier,
    };
  },
});
