  },
  computed: {
    time() {
      return this.msg.timestamp || this.msg.insertTimestamp;
    },
    isMine() {
      const current = getContextUser();
      return current === this.msg.sender;
    },
    isBubble() {
      const { msg } = this;
      return msg.elems.length > 1
        || (msg.elems.length === 1 && (msg.elems[0].type === 'text' || msg.elems[0].type === 'sound' || msg.elems[0].type === 'image'));
