























import uploadToOSS from '@/utils/oss/uploadToOSS';
import selectFiles from '@/utils/select-files';
import { validFiles } from '@/utils/validators/valid-file';
import { defineComponent, ref } from '@vue/composition-api';

const key2Name = {
  thumbnail: '垂悬图',
  flat: '平铺图',
  texture: '纹理图',
} as const;

const UploadImgJson = defineComponent({
  name: 'UploadImgJson',
  props: {
    imgJson: {
      type: Object as () => { thumbnail: string, flat: string, texture: string },
      default: () => ({}),
      // type: Object,
      // required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  model: {
    prop: 'imgJson',
    event: 'change',
  },
  setup(props, { emit, root }) {
    // 已选择的file文件
    const selectedFils = ref<{ [key: string]: File | null }>({
      thumbnail: null,
      flat: null,
      texture: null,
    });

    const choseImg = async (type: 'thumbnail' | 'flat' | 'texture') => {
      if (props.disabled) return;
      const files = await selectFiles(['png', 'jpg'], 5000);
      validFiles(files);
      [selectedFils.value[type]] = files;
      emit('change', { ...props.imgJson, [type]: URL.createObjectURL(files[0]) });
      // debugger;
      // root.$set(props.imgJson, type, URL.createObjectURL(files[0]));
    };


    const remove = (type: 'thumbnail' | 'flat' | 'texture') => {
      selectedFils.value[type] = null;
      emit('change', { ...props.imgJson, [type]: '' });
      // root.$set(props.imgJson, type, null);
    };

    async function previewImage(type: 'thumbnail' | 'flat' | 'texture') {
      // const imgList = Object.values(props.imgJson).filter(i => !!i);
      const imgList = [props.imgJson[type]];
      await root.$ImageViewer.show(imgList as string[], {
        srcList: imgList,
        targetElems: `.item-image-${type}`,
      });
    }


    // 外面调用
    const uploadImgJson = async () => {
      const bucketList = {
        thumbnail: 'FUR',
        flat: 'FLAT',
        texture: 'FLAT',
      };
      const newImgJson = JSON.parse(JSON.stringify(props.imgJson));
      await Promise.all(Object.entries(selectedFils.value).map(async ([key, item]) => {
        if (!item) return;
        newImgJson[key] = await uploadToOSS(item, bucketList[key]);
      }));

      return newImgJson;
    };

    return {
      choseImg,
      remove,
      previewImage,
      uploadImgJson,
      key2Name,
    };
  },
});

export default UploadImgJson;
