
/**
 * @see {@link https://gist.github.com/gre/1650294}
 */

export const easing = ['quad', 'cubic', 'quart', 'quint'].reduce((memo, power, p) => {
  memo[power] = {
    in(t) {
      return t ** (p + 2);
    },
  };
  return memo;
}, {});

easing.elastic = {
  in(k) {
    let s;
    let a = 0.1;
    const p = 0.4;
    if (k === 0) return 0;
    if (k === 1) return 1;
    if (!a || a < 1) { a = 1; s = p / 4; } else s = p * Math.asin(1 / a) / (2 * Math.PI);
    const result = -(a * (2 ** (10 * (k -= 1))) * Math.sin((k - s) * (2 * Math.PI) / p));
    return result;
  },
};
easing.bounce = {
  out(k) {
    let result;
    if (k < (1 / 2.75)) {
      return 7.5625 * k * k;
    } if (k < (2 / 2.75)) {
      result = 7.5625 * (k -= (1.5 / 2.75)) * k + 0.75;
      return result;
    } if (k < (2.5 / 2.75)) {
      result = 7.5625 * (k -= (2.25 / 2.75)) * k + 0.9375;
      return result;
    }
    result = 7.5625 * (k -= (2.625 / 2.75)) * k + 0.984375;
    return result;
  },
};

Object.keys(easing).forEach((key) => {
  const easingObj = easing[key];
  easingObj.out = easingObj.out || ((t) => 1 - easingObj.in(1 - t));
  easingObj.in = easingObj.in || ((t) => 1 - easingObj.out(1 - t));
  easingObj.inOut = easingObj.inOut
  || ((t) => (t < 0.5 ? easingObj.in(t * 2) / 2 : easingObj.out(t * 2 - 1) / 2 + 0.5));
});
easing.linear = x => x;


const defaults = {
  during: 300,
  from: 0,
  to: 1,
  easing: easing.linear,
  raf: global.requestAnimationFrame.bind(global),
};
/**
 * 动画函数，指定初始值以及结束值，fn将会被依次调用
 * @param {function} fn 补间函数，每一个渲染时会调用该函数
 * @param {object} options 动画的配置
 * @param {number} options.during 动画持续的时间，默认300
 * @param {number} options.from 动画值的初始值
 * @param {number} options.to 动画值的结束值
 * @param {function} options.easing 动画缓动函数
 * @param {function} options.raf requestAnimationFrame函数，当然你可以自定义
 */
export function animate(fn, options) {
  if (typeof options === 'number') {
    options = {
      during: options,
    };
  }
  if (typeof options === 'function') {
    options = {
      easing: options,
    };
  }
  options = { ...defaults, ...options };
  let start;
  let end;
  function run() {
    const now = Date.now();
    if (!start) {
      start = now;
      end = start + options.during;
    }
    fn((options.to - options.from) * options.easing((now - start) / options.during) + options.from);
    if (now < end) options.raf(run);
  }
  run();
}
animate.easing = easing;

export default animate;
