import {
  computed, defineComponent, ref, watch,
} from '@vue/composition-api';

const numberType = defineComponent({
  props: {
    builder: {
      type: String,
      default: 'el-input',
    },
    val: {
      type: [String, Number],
      default: '',
    },
    toFixed: {
      type: Number,
      default: 2,
    },
    max: {
      type: Number,
      default: Infinity,
    },
    min: {
      type: Number,
      default: -Infinity,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  model: {
    prop: 'val',
    event: 'change',
  },
  render() {
    const s = this as InstanceType<typeof numberType>;
    const { builder, disabled } = s;
    return <builder value={s.parsedValue} oninput={s.handleUpdateValue}
    disabled={ disabled }
    onfocus={(e: Event) => s.$emit('focus', e)}
    onblur={(e: Event) => s.$emit('blur', e)}/>;
  },
  setup(props, { emit }) {
    const hasDot = computed(() => String(props.val).includes('.'));
    const parsedValue = ref('');
    watch(() => props.val, () => {
      if (+props.val === 0 || globalThis.isNaN(+props.val)) parsedValue.value = '';
      else parsedValue.value = String(props.val);
    }, {
      immediate: true,
    });
    function updateParsedValue(value: string) {
      parsedValue.value = value;
    }
    function handleUpdateValue(newStr: string) {
      // 禁止多个小数点，最多一位
      if (newStr.indexOf('.') !== newStr.lastIndexOf('.')) return;
      let leng;
      let short;
      if (newStr.length > parsedValue.value.length) {
        leng = newStr;
        short = parsedValue.value;
      } else {
        short = newStr;
        leng = parsedValue.value;
      }
      // 有一个小数点时，这个小数点无论是新增的还是要删掉的都合法
      if (leng[leng.length - 1] === '.') {
        updateParsedValue(newStr);
        return;
      }

      // 允许1.0 1.00 1.000
      if (/\.[0-9]?[0-9]?0$/.test(leng)) {
        updateParsedValue(newStr);
        return;
      }

      // 允许第一位是减号
      if (leng.includes('-')) {
        if (leng.lastIndexOf('-') !== 0) return;
        if (short === '-' || leng === '-') {
          updateParsedValue(newStr);
          return;
        }
      }
      // 限制小数位长度
      if (hasDot.value && newStr.length - 1 - newStr.indexOf('.') > props.toFixed) return;
      if (newStr && globalThis.isNaN(+newStr)) return;
      // 验证成功，已是数字
      if (+newStr > props.max) return;
      if (+newStr < props.min) return;
      // 验证成功，已是合法数字
      emit('change', +newStr);
      emit('input', +newStr);
    }
    return {
      parsedValue,

      handleUpdateValue,
    };
  },
});


export default numberType;
