import { axios } from '@/utils/axios';

export function getRelativePathFromUrl(url: string) {
  if (/\/datapipe\/repository\//.test(url)) {
    return url.match(/\/datapipe\/repository\/(.+)\?/)?.[1] || '';
  }
  return url.match(/\.aliyuncs\.com\/(.+?)(\?|$)/)?.[1] || '';
}

export const getModelCTMList = (modelId:string, modelType:'SHOE'|'HANDBAG') => {
  return axios({
    method: 'get',
    url: 'show/application/model/ctms',
    params: {
      modelId,
    },
    headers: {
      'Resource-Checkout': true,
      'requester-type': 'web',
      'model-type': modelType,
    },
  });
};


export default {};
