<template>
  <div class="history">
    <div v-loading="sess === undefined" class="history-wrap" ref="list" @scroll="_onScroll">
      <div class="messages" v-if="sess">
        <div class="load-more">
          <template v-if="sess.hasHistory !== false">
            <template v-if="sess.isLoadingHistory">{{$t('file-list.im.loading_info')}} </template>
            <template v-else>{{$t('file-list.im.load_more')}}</template>
          </template>
          <template v-else>{{$t('file-list.im.top_of_message')}} </template>
        </div>
        <template v-for="(item, index) in sess.msgs">
          <div class="interval" v-if="isShowInterval(index)" :key="`${index}-interval`">
            {{(item.timestamp || item.insertTimestamp) | date('MM/DD/YYYY HH:mm')}}
          </div>
          <SysMsg
            :key="index"
            @click.native="showMsg(item)"
            style="margin-bottom: 25px" :msg="item"
            v-if="item.elems[0].type === 'system'" />
          <UserMsg
            v-else
            :key="index"
            @click.native="showMsg(item)"
            style="margin-bottom: 25px" :msg="item">
            <slot name="user-name" slot="user-name" :msg="item"></slot>
            <slot name="user-img" slot="user-img" :msg="item"></slot>
          </UserMsg>
        </template>
      </div>
    </div>
  </div>

</template>
<style scoped>
.history {
  position: relative;
}
.history-wrap {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  overflow-y: auto;
  padding: 20px 15px;
}

:hover::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.2);
}
::-webkit-scrollbar {
  width: 7px;
  background-color: transparent;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: transparent;
  transition: background-color 0.5s;
}
.interval,
.load-more {
  font-size: 13px;
  color: #cfd2d8;
  text-align: center;
  margin-bottom: 25px;
}
</style>
<script>
import animate from '@/utils/animate';
import UserMsg from './UserMsg.vue';
import SysMsg from './SystemMsg.vue';

async function getIntersectionObserver() {
  if (!IntersectionObserver) {
    await import('intersection-observer');
  }
  return IntersectionObserver;
}
export default {
  components: { UserMsg, SysMsg },
  props: [
    // 'type',
    'id',
  ],
  data() {
    return {
      sess: undefined,
      interval: 2 * 60 * 1000,
      isIntersection: true,
    };
  },
  computed: {
    sessOptions() {
      return { id: this.id };
    },
  },
  destroyed() {
    if (this.sess) {
      this.sess.setAutoRead(false);
    }
  },
  methods: {
    showMsg(msg) {
      console.info(msg);
      // this.$im.trigger('received', [msg, this.sess]);
    },
    isShowInterval(index) {
      const lastIndex = index - 1;
      const aMsg = this.sess.msgs[index];
      const bMsg = this.sess.msgs[lastIndex];
      return lastIndex === -1
        || ((aMsg.timestamp ?? aMsg.insertTimestamp)
          - (bMsg.timestamp ?? aMsg.insertTimestamp)) > this.interval;
    },
    async _onScroll() {
      const container = this.$refs.list;
      if (container && container.scrollTop === 0
        && this.sess
        && this.sess.hasHistory !== false
        && !this.sess.isLoadingHistory) {
        await this.sess.getHistory(10);
      }
    },
    async scrollToBottom() {
      this.$refs.list.scrollTop = this.$refs.list.scrollHeight;
    },
    applyAutoScroll() {
      let scrollHeight = 0;
      return this.$watch(() => {
        const { sess } = this;
        if (!sess) return undefined;
        const { msgs } = sess;
        if (!msgs.length) return undefined;
        return {
          firstMessage: msgs[0],
          lastMessage: msgs[msgs.length - 1],
          length: msgs.length,
        };
      }, (newValue, oldValue) => {
        const container = this.$refs.list;
        if (!container) {
          return false;
        }

        if (!newValue) {
          this.$nextTick(() => {
            scrollHeight = 0;
            container.scrollTop = 0;
          });
        } else if (!oldValue) {
          this.$nextTick(() => {
            ({ scrollHeight } = container);
            container.scrollTop = container.scrollHeight;
          });
        } else {
          const isGetHistory = newValue.lastMessage === oldValue.lastMessage;
          const isInConcern = scrollHeight - container.scrollTop <= container.clientHeight + 10;
          this.$nextTick(() => {
            if (
              isGetHistory || isInConcern
            ) {
              const newScrollTop = container.scrollHeight - scrollHeight + container.scrollTop;
              // TODO: 暂时不加上动画
              if (isGetHistory) {
                container.scrollTop = newScrollTop;
              } else {
                animate((x) => {
                  container.scrollTop = x;
                }, {
                  from: container.scrollTop,
                  to: newScrollTop,
                  during: 100,
                });
                /*
                 $(container).animate({
                 scrollTop: newScrollTop
                 }, 'fast');
                 */
              }
            }
            ({ scrollHeight } = container);
          });
        }
        return undefined;
      });
    },
    async applyDetectIntersection() {
      const IntersectionObserver = await getIntersectionObserver();
      const io = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
          this.isIntersection = entry.intersectionRatio > 0;
        });
      });
      io.observe(this.$el);
    },
  },
  watch: {
    sess(sess, oldSess) {
      if (sess) {
        sess.setAutoRead(true);
      }
      if (oldSess) {
        oldSess.setAutoRead(false);
      }
    },
    isIntersection(is) {
      this.sess.setAutoRead(is);
      if (is) {
        this.scrollToBottom();
      }
    },
  },
  created() {
    this.$watch(() => {
      if (!this.id) {
        return undefined;
      }
      return this.$im.getSession({ ...this.sessOptions, msgCount: 15 }, true);
    }, async (sess) => {
      this.sess = await sess;
      if (this.sess.type === 1 && Object.values(this.sess.members).length === 0) {
        this.sess.getMembers();
      }
    }, {
      immediate: true,
    });
  },
  async mounted() {
    this.applyAutoScroll();
    this.applyDetectIntersection();
  },
};
</script>
