















































import {
  defineComponent, computed,
} from '@vue/composition-api';
// import ModelInfo from '@/views/model/info';
// import MaterialInfo from '@/views/material/info';
import Catalog, { CatalogInfoFile } from '@/models/Catalog';
import downloadByUrl from '@/utils/download-by-url';
import getExt from '_/get-ext';
import { getTypeFormExt } from '../../../../utils/get-file-validation-options';

type FileCatalog = Catalog<CatalogInfoFile>;


function rpx(x: number, size: number = 1) {
  return `${(x * size)}px`;
  // return `${($size / $baseFontSize)}em`;
}

const ItemBinaryFile = defineComponent({
  name: 'ItemBinaryFile',
  props: {
    item: {
      required: true,
      type: Object as () => FileCatalog,
    },
    itemSize: {
      default: () => ({
        defaultWidth: 220,
        scale: 1,
      }),
      type: Object,
    },
    shownInfo: {
      type: Boolean,
      default: false,
    },
  },
  setup(p, { emit }) {
    const relateType = computed(() => p.item.relateType);
    const fileInfo = computed(() => p.item.fileInfo);

    const elemStyles = computed(() => {
      const { itemSize } = p;
      return {
        img: {
          width: rpx(itemSize.defaultWidth, itemSize.scale),
          height: rpx(152, itemSize.scale),
        },
        // img: {
        //   height: rpx(152, itemSize.scale),
        // },
        infoContainer: {
          // fontSize: rpx(14, 1),
          // height: rpx(41, 1),
          // padding: `${rpx(10, 1)}`,
          width: rpx(220, itemSize.scale),
        },
      };
    });
    const imageSrc = computed(() => {
      return p.item.contentThumbnail;
    });
    const type = computed(() => {
      if (!fileInfo.value) return '';
      let ext = '';
      const { downloadPath: path } = fileInfo.value;
      if (path) ext = getExt(path);
      return getTypeFormExt(ext);
    });
    const defaultImageSrc = computed(() => {
      const UNKNOWN_ICON = require('@/assets/img/file-list/raw-file-icon/text.png');
      const vType = type.value;
      if (vType) {
        // eslint-disable-next-line import/no-dynamic-require
        return require(`@/assets/img/file-list/raw-file-icon/${vType}.png`);
      }
      return UNKNOWN_ICON;
    });
    async function handleDblClick() {
      const vType = type.value;
      const downloadPath = p.item.fileInfo?.downloadPath;
      if (downloadPath) {
        if (vType === 'img' || vType === 'gif') {
          emit('preview', { item: p.item });
        } else {
          const name = p.item.fileInfo?.name ?? p.item.name;
          downloadByUrl(downloadPath, name);
        }
      }
    }

    const creator = computed(() => p.item.fileInfo?.creatorUser);
    return {
      imageSrc,
      creator,
      defaultImageSrc,
      relateType,
      elemStyles,
      fileInfo,
      handleDblClick,
    };
  },
});

export default ItemBinaryFile;
