import axios from '@/utils/axios';
import {
  attribute2JSONschema, IAttribute, jsonschema2FormItemValidatorObject,
} from '@/components/anta-customer-files/attribute';
import useLoader from '_/composables/use-loader';
import extract from '_/extract';
import {
  computed,
  defineComponent, reactive, ref, toRefs,
} from '@vue/composition-api';
import SeasonSelect from '@/views/material/info-anta/components/FormComponents/SeasonSelect/index.vue';
import PriceFormItem from '@/views/material/info-anta/components/FormComponents/PriceFormItem';
import SupplierSelect from '@/views/material/info-anta/components/FormComponents/SupplierSelect';
import MaterialTypeCascade from '@/components/MaterialTypeCascade.vue';
import Jsonschema2Form from '@4dst-saas/form-utils/dist/components/jsonschema2form';
import Jsonschema from '@4dst-saas/form-utils/dist/components/jsonschema2form/JSONschema';
import difference from '@4dst-saas/public-utils/dist/difference';

export default defineComponent({
  name: 'AntaMiddleClass2Form',
  components: {
    SeasonSelect,
    PriceFormItem,
    SupplierSelect,
    MaterialTypeCascade,
  },
  props: {
    middleClassId: {
      type: String, required: true,
    },
    formData: {
      type: Object as () => { [key: string]: any },
      default: () => {
        return {
        // eslint-disable-next-line quote-props
          // '测试下拉': '1306902076941131778',
        };
      },
    },
    validateDropdownItemImmediatly: { type: Boolean, default: true },
    jsonschemaListHander: {
      type: Function as unknown as () => (jss: Jsonschema[]) => Jsonschema[],
    },
    showValidateButton: { type: Boolean, default: false },
  },
  render() {
    if (!this.middleClassId || this.notHasTemplate) {
      return <div></div>;
    }
    // if (!this.jsonschema) {
    //   return <div vLoading={true} style={{
    //     height: '300px', width: '100%',
    //   }}></div>;
    // }
    // return <AnalysisWrapper jsonschema={this.jsonschema} formData={this.formData}
    // form={this.formData}></AnalysisWrapper>;
    const j2f = <Jsonschema2Form ref='formRef' jsonschema={this.jsonschema} {...{ attrs: this.$attrs }}
      // formItemValidator={this.formItemValidator}
      form={this.validateDropdownItemImmediatly ? this.formData : (this.form || this.formData)}
      onchange={(v: any) => this.$emit('change', v)}
      labelPosition='top' showValidateButton={this.showValidateButton}
      ></Jsonschema2Form>;
    const style = {
      height: '200px',
      width: '100%',
    };
    return this.jsonschema ? j2f : <div vLoading={true} style={style}>
      <div vShow={false}>{j2f}</div>
    </div>;
  },
  setup(props, { emit }) {
    // let t1:any = null;
    const propertylist = useLoader(async ({ middleClassId: id }) => {
      if (!id) return null;
      const r = (await axios({
        url: '/anta-material/template/middle',
        params: { id },
        method: 'GET',
      })).data as { propertyId?: string, sort: number, isSystem?: boolean, key: string, title: string, required: 0|1}[];
      if (!r.length) emit('error', new Error('对应的中类没有模板，请更换一个有模板的中类'));
      return r.sort((a, b) => a.sort - b.sort);
      // return t1;
    }, extract(toRefs(props), ['middleClassId']), {
      initialValue: null,
      loadingValue: (lastResult) => lastResult,
    });

    // let t2:any = null;
    const jsonschema = useLoader(async ({ propertylist: pList }) => {
      if (!pList || !pList.length) return null;

      const list = await Promise.all(pList.map(async (propertyItem) => {
        if (propertyItem.isSystem) {
          return propertyItem;
        }
        return {
          ...(await axios({
            url: `/anta-material/dynamics/property/detail?id=${propertyItem.propertyId}`, method: 'GET',
          })).data,
          required: !!propertyItem.required,
        } as IAttribute;
      }));
      let jssList = attribute2JSONschema(list) as Jsonschema[];

      if (props.jsonschemaListHander) jssList = props.jsonschemaListHander(jssList);
      // t2 = jssList;
      return jssList;
      // const res = JSONschemaList2JSONschema(jssList);
      // return res;
    }, reactive({ propertylist }), { initialValue: null, loadingValue: (lastResult) => lastResult });
    const formRef = ref<InstanceType<typeof Jsonschema2Form>>();

    // const formItemValidator = useLoader(({ jsonschema: _jss }) => {
    //   if (!_jss) return null;
    //   const res = jsonschema2FormItemValidatorObject(_jss);
    //   // console.log(res, res['测试下拉']('6'));
    //   return res;
    // }, reactive({ jsonschema }));

    const form = useLoader(async ({ jsonschema: _jss, formData: _form }) => {
      if (!_jss || !Object.keys(_form).length) return _form;
      // const properties = _jss.properties!;
      // const propertiesKeys = Object.keys(properties);
      const formKeys = Object.keys(_form);
      const shouldHandleKeys = formKeys.filter(key => {
        if (_jss.find(i => i.prop === key || i.title === key)) return true;
        return false;
      });
      const shouldFetchList: {key: string, id: string}[] = [];
      shouldHandleKeys.forEach(key => {
        const jss = _jss.find(i => i.prop === key || i.title === key)!;
        if (!jss.oneOf && !jss.anyOf) return;
        const currentValue = _form[key]! as string | string[];
        if (Array.isArray(currentValue)) {
          const constList = jss.anyOf!.map(item => item.const) as unknown as {
            label: string, value: string,
          }[];
          const notFounds = currentValue.filter(id => !constList.find(i => i.value === id));
          if (notFounds.length) {
            const founds = difference(currentValue, notFounds);
            _form[key] = founds;
            shouldFetchList.push(...notFounds.map(id => ({ id, key })));
          }
        } else {
          const constList = jss.oneOf!.map(item => item.const) as unknown as {
            label: string, value: string,
          }[];
          const target = constList.find(i => i.value === currentValue);
          if (!target) shouldFetchList.push({ key, id: currentValue });
        }
      });
      await Promise.all(shouldFetchList.map(async ({ key, id }) => {
        const value = (await axios({
          url: '/anta-material/dynamics/property/value/id',
          method: 'GET',
          params: { id },
        })).data as string;
        if (Array.isArray(_form[key])) {
          _form[key].push(value);
        } else {
          _form[key] = value;
        }
      }));
      return _form;
    }, reactive({ jsonschema, ...extract(toRefs(props), ['formData']) }));

    // watchEffect(async () => {
    //   if (jsonschema.value) {
    //     await delay(500);
    //     Object.assign(form.value, { ...props.formData });
    //     Object.assign(props.formData, form.value);
    //   }
    // });
    async function validate() {
      // 没有模板时，不用校验
      if (!formRef.value) return true;
      return formRef.value.validate();
    }
    return {
      formRef,
      jsonschema,
      // formItemValidator,
      notHasTemplate: computed(() => !!propertylist.value?.map(p => p.propertyId) && !propertylist.value?.map(p => p.propertyId).length),
      form,
      validate,
    };
  },
});
