
/* eslint-disable vue/no-side-effects-in-computed-properties */

import {
  computed,
  defineComponent, ref,
} from '@vue/composition-api';
import bubbleUpEvents from '@/utils/bubble-up-events';

import { stop, prevent, self } from '_/jsx-utils';
import CatalogModel, { CatalogModalTagMap } from '@/models/Catalog';
import PanelItemFile from './ItemFile.vue';
import PanelItemMaterial from './ItemMaterial.vue';
import PanelItemFolder from './ItemFolder.vue';
import PanelItemBinaryFile from './ItemBinaryFile.vue';

const itemGlobalIsDragging = ref(false);

export const PanelItem = defineComponent({
  name: 'CatalogItem',
  props: {
    itemSize: {
      default: () => {
        return {
          folder: {
            defaultWidth: 220,
            scale: 1,
          },
          file: {
            defaultWidth: 220,
            scale: 1,
          },
          binaryFile: {
            defaultWidth: 220,
            scale: 1,
          },
          material: {
            defaultWidth: 220,
            scale: 1,
          },
        };
      },
      type: Object,
    },
    viewType: {
      type: String,
      default: 'drape',
    },
    shownInfo: {
      type: Boolean,
      default: false,
    },
    item: {
      type: Object as unknown as () => CatalogModel,
      required: true,
    },
    isSelected: {
      type: Boolean,
      default: false,
    },
    isCopying: {
      type: Boolean,
      default: false,
    },
    isCutting: {
      type: Boolean,
      default: false,
    },
    isDragging: {
      type: Boolean,
      default: false,
    },
    // multi: Boolean,
    draggingData: Object as () => any,
    clipboard: Object as () => any,
    tagMap: {
      type: Object as () => CatalogModalTagMap,
    },
  },
  setup(props, { emit }) {
    const isDraggingOver = ref(false);

    const tags = computed(() => {
      return props.tagMap && props.tagMap[props.item.id];
    });

    const handleDragstart = (e: DragEvent) => {
      itemGlobalIsDragging.value = true;
      const { item } = props;
      if (item.fileType) {
        const ghost = (e.target as Element).querySelector('.panel-item-type');
        if (ghost && e.dataTransfer) {
          const rect = ghost.getBoundingClientRect();
          e.dataTransfer.setDragImage(ghost, e.clientX - rect.x, e.clientY - rect.y);
        }
      }
      emit('drag', item, e);
    };
    const handleDragEnter = (e: Event) => {
      isDraggingOver.value = true;
    };
    const handleDragLeave = (e: Event) => {
      isDraggingOver.value = false;
    };

    const handleDrop = (e: DragEvent) => {
      const { item } = props;
      isDraggingOver.value = false;
      emit('drop', item, e);
    };

    const handleDragend = () => {
      itemGlobalIsDragging.value = false;
      props.draggingData.clear();
    };

    // onUpdated(() => {
    //   console.log('updated==========');
    // });
    return {
      isDraggingOver,
      handleDragEnter,
      handleDragLeave,
      handleDragstart,
      handleDrop,
      handleDragend,
      tags,
    };
  },
  render() {
    const that = this;
    const {
      isSelected, isCutting, isCopying,
      isDragging, item, itemSize, viewType, shownInfo,
      isDraggingOver,
      handleDragstart,
      handleDrop,
      handleDragend,
      handleDragEnter,
      handleDragLeave,
      tags,
    } = that;

    // 判断该使用的item组件
    const judgeComponent = (relateType: null | number) => {
      switch (relateType) {
        case 100_000: return PanelItemFolder;
        case 0:
        case 3:
        case 4: return PanelItemFile;
        case 1: return PanelItemMaterial;
        case 2: return PanelItemBinaryFile;

        default: return null;
      }
    };

    // 判断该使用的itemSize配置
    const judgeItemSize = (relateType: null | number) => {
      switch (relateType) {
        case 100_000: return itemSize.folder;
        case 0:
        case 3:
        case 4: return itemSize.file;
        case 1: return itemSize.material;
        case 2: return itemSize.binaryFile;

        default: return null;
      }
    };
    const ItemComponent = judgeComponent(that.item.relateType as null | number);
    const itemComponentSize = judgeItemSize(that.item.relateType as null | number);

    const isFolder = item.fileType === 0;
    const isDraggable = !item.appType;
    return <div class={{
      active: isSelected,
      dragging: itemGlobalIsDragging.value,
      'is-dragging': isDragging,
      'is-folder': isFolder,
      'is-dragging-over': isDraggingOver,
      'is-cutting': isCutting,
      'is-copying': isCopying,
    }}
      draggable={isDraggable}
      on={{
        mousedown: stop.handler,
        click: stop(() => { that.$emit('click', item); }),
        contextmenu: stop.prevent((e) => { that.$emit('contextmenu', e, item); }),
        dragover: prevent.handler,
        ...(isDraggable ? {
          dragstart: handleDragstart,
        } : {}),
        drop: stop.prevent(handleDrop),
        dragend: handleDragend,
        dragleave: handleDragLeave,
        dragenter: stop.prevent(handleDragEnter),
      }}
    >
      {
        ItemComponent
          ? <ItemComponent
            class="panel-item-type"
            item={item}
            itemSize={itemComponentSize}
            viewType={viewType}
            shownInfo={shownInfo}
            on={bubbleUpEvents.call(that, ['preview', 'enterFolder', 'rename', 'refresh'])}
            tags={tags}
          />
          : null
      }


    </div>;
  },
});
export type PanelItemConstructor = typeof PanelItem;
export default PanelItem;
