





























import {
  defineComponent, ref, computed, onMounted,
} from '@vue/composition-api';

import axios from '@/utils/axios';
import { blockDragEffect } from '@/utils/public';
import { getContextUser } from '@/loaders/context';
import { getCurrentCompany } from '@/loaders/company';

import MaterialForm from './Form.vue';
import ModelsSelector from './ModelsSelector.vue';
import AttachmentImage from './AttachmentImage.vue';
import AttachmentVideo from './AttachmentVideo.vue';


/**
 * 更新材质详情信息（标签更新在其他接口）
 * @param {String} data { id: string, propName: any }
 * @see {Promise}
 */
export const updateMaterialInfo = (data: any) => {
  return axios({
    method: 'post',
    url: '/bff-center/showCommon/updateNewMaterialInfo',
    data,
  });
};


// 判断材质修改提醒
async function fetchShowAntaNote(originalId: string) {
  return (await axios({
    method: 'GET',
    url: '/show/material/relate/needAlert',
    params: { originalId },
  })).data;
}

export default defineComponent({
  name: 'MaterialDetailForm',
  components: {
    ModelsSelector,
    AttachmentImage,
    AttachmentVideo,
    MaterialForm,
  },
  props: {
    material: {
      type: Object,
      required: true,
    },
    saveDisabled: {
      type: Boolean,
      default: true,
    },
    entryAppType: {
      type: Number,
    },
  },
  // @ts-ignore
  setup(props, { emit, refs, root: { $errHandler, $message } }) {
    const materialForm = ref(null);

    const isCreator = computed(() => getContextUser()?.id === props.material.creator);

    const materialCopy = ref(JSON.parse(JSON.stringify(props.material)));

    // 禁止浏览器拖拽的默认行为
    onMounted(() => blockDragEffect(refs.uploadArea));

    // 保存
    async function save() {
      emit('loading', true);
      try {
        const isSupplier = getCurrentCompany()?.type === 3;
        console.info(getCurrentCompany());
        if (isSupplier) {
          const t = await fetchShowAntaNote(props.material.originalId);
          console.info(t);
          if (t) {
            $message.warning('此面料已交付品牌使用，信息修改需确定是否影响交付面料，若有影响，请自行修改或联系品牌修改');
          }
        }

        const updateObj = await materialForm.value!.getFormData();
        console.info(updateObj);

        updateObj.id = props.material.id;
        // @ts-ignore
        updateObj.assetsInfo = {}; // 初始assetsInfo接口有时返回null，访问 models, images, videos会报错

        // 获取模型选择的结果
        const models = await refs.modelsSelector.getSelectResult();
        updateObj.assetsInfo.models = models.map((model: any) => {
          return { id: model.id, cover: model.modelCover };
        });

        // 获取材质图片的结果
        const images = await refs.attachmentImage.getImageOssPaths();
        updateObj.assetsInfo.images = images;

        // 获取模型选择的结果
        const videos = await refs.attachmentVideo.getVideoOssPaths();
        updateObj.assetsInfo.videos = videos;

        await updateMaterialInfo(updateObj);
        emit('switch-component', 'ComponentPreviewDetail');
        // emit('update:saveDisabled', true);
        emit('refresh');
        emit('update:saveDisabled', true);
      } catch (err) {
        $errHandler(err);
      } finally {
        emit('loading', false);
      }
    }


    return {
      isCreator,
      save,
      materialCopy,
      materialForm,
    };
  },
});
