































































































import { defineComponent, computed, getCurrentInstance } from '@vue/composition-api';
import {
  getFileRelateIdByCatalogId,
} from '@/loaders/catalog';
import { isQt } from '@/utils/is-qt';
// import ModelInfo from '@/views/model/info';
// import MaterialInfo from '@/views/material/info';
import { useEl } from '_/composables/use-el';
import { CatalogInfoMaterial, CatalogMaterial } from '@/models/Catalog';
import MaterialInfo from '@/views/material/info/index.vue';
import AntaMaterialInfo from '@/views/material/info-anta/index.vue';
import DeclineRecord from '@/views/material/info-anta/components/DeclineRecord.vue';
import { i18n } from '@/i18n';
import useClosest from '_/composables/use-closest';
import { getRelativePathFromUrl } from '@/utils/normalize-component';
import openQtWin from './open-qt-win';

function rpx(x: number, size: number = 1) {
  return `${(x * size)}px`;
  // return `${($size / $baseFontSize)}em`;
}
type ViewType = 'drape' | 'flat' | 'texture' | 'default';
const viewType2ThumbPropKey = {
  default: 'flat',
  drape: 'thumbnail',
  flat: 'flat',
  texture: 'texture',
} as const;


const ItemFile = defineComponent({
  name: 'CatalogItemFile',
  props: {
    item: {
      required: true,
      type: Object as () => CatalogMaterial,
    },
    itemSize: {
      default: () => ({
        defaultWidth: 220,
        scale: 1,
      }),
      type: Object,
    },
    viewType: {
      default: 'drape',
      type: String as () => ViewType,
    },
    shownInfo: {
      type: Boolean,
      default: false,
    },
  },
  setup(p, { emit, root }) {
    const _this = getCurrentInstance();
    const relateType = computed(() => p.item.relateType);

    const material = computed(() => p.item.fileInfo);

    const 材料规格 = computed(() => {
      return p.item.fileInfo?.extraProperty?.材料规格 ?? p.item.fileInfo?.material_specification;
    });

    // 控制缩放的样式
    const elemStyles = computed(() => {
      const { itemSize } = p;
      return {
        img: {
          width: rpx(itemSize.defaultWidth, itemSize.scale),
          height: rpx(152, itemSize.scale),
        },
        // img: {
        //   height: rpx(152, itemSize.scale),
        // },
        infoContainer: {
          width: rpx(220, itemSize.scale),
          // fontSize: rpx(14, 1),
          // height: rpx(41, 1),
          // padding: `${rpx(10, 1)} ${rpx(10, 1)}`,
        },
      };
    });

    // 缩略图url
    const imageSrc = computed(() => {
      const { fileInfo: _fileInfo } = p.item as CatalogMaterial;
      let computedView = '';
      if (p.viewType === 'default') {
        if (_fileInfo.materialType === 6) {
          computedView = _fileInfo?.[viewType2ThumbPropKey.texture] ?? _fileInfo?.[viewType2ThumbPropKey.drape] ?? '';
        }
        computedView = _fileInfo?.[viewType2ThumbPropKey.drape] ?? '';
      }
      if (p.viewType === 'flat') {
        computedView = _fileInfo?.[viewType2ThumbPropKey.texture] ?? _fileInfo?.[viewType2ThumbPropKey.flat] ?? '';

        // return _fileInfo?.[viewType2ThumbPropKey.drape] ?? '';
      } else {
        computedView = _fileInfo?.[viewType2ThumbPropKey[p.viewType as ViewType]] ?? '';
      }

      return computedView
        || _fileInfo?.[viewType2ThumbPropKey.flat]
        || _fileInfo?.[viewType2ThumbPropKey.drape]
        || _fileInfo?.[viewType2ThumbPropKey.texture]
        || null;
    });

    // 默认样式（鞋款）
    const defaultImageSrc = computed(() => {
      return require('@/assets/img/file-list/material-default.png');
    });

    // 成本单价
    const costPrice = computed(() => {
      const _material = material.value;
      const supplierPriceObj = _material.supplier_price_obj ?? ({} as NonNullable<CatalogInfoMaterial['supplier_price_obj']>);
      const unit2symbol = {
        1: '￥',
        2: '$',
      };
      let v: number;
      let u: 1 | 2;
      if (supplierPriceObj.cost_value) {
        v = supplierPriceObj.cost_value;
        u = supplierPriceObj.cost_unit;
      } else if (supplierPriceObj.reference_price_value) {
        v = supplierPriceObj.reference_price_value;
        u = supplierPriceObj.reference_price_unit;
      } else {
        v = _material.samplePrice;
        u = _material.samplePriceUnit;
      }
      return u && v ? `${unit2symbol[u]}${v}` : '';
    });

    const el = useEl();


    // 打开面料详情
    async function handleShowMaterialDetail(item: CatalogMaterial) {
      const { relateType: _relateType, id, workspaceId } = item;

      let dialogResult;
      // eslint-disable-next-line no-unused-expressions

      let relateId: string | undefined;
      if (_relateType !== undefined) {
        relateId = await getFileRelateIdByCatalogId({
          catalogId: id,
          workspaceId,
          relateType: _relateType,
        });
      } else {
        ({ relateId } = p.item);
      }


      if (el.value) {
        el.value.blur();
      }

      const businessType = item.fileInfo.businessType ?? 0;

      if (isQt()) {
        let url = '';
        if (businessType === 1) {
          url = `/qt/dialog/anta-material/info?id=${relateId}${workspaceId ? `&groupAppId=${workspaceId}` : ''}`;
        } else {
          url = `/qt/dialog/material/info?id=${relateId}${workspaceId ? `&groupAppId=${workspaceId}` : ''}`;
        }
        dialogResult = await openQtWin({ url, query: { language: i18n.language } });
      } else {
        dialogResult = await root.$injectDialog({
          is: businessType === 1 ? AntaMaterialInfo : MaterialInfo,
          props: {
            entryAppType: p.item.entryAppType,
            groupAppId: item.workspaceId,
            id: relateId,
          },
        }, {
          props: {
            // title: i18n.t('file-list.catalog.material_info'),
            // width: '703px',
            top: 'auto',
            bottom: 'auto',
          },
        });
      }

      // const originList = useClosest('PanelList').value;
      // const originList = useClosest('PanelList');
      // const _this = getCurrentInstance();
      const originList = _this?.$closest('Catalog');
      if (!originList) {
        return dialogResult;
      }
      const originItem = originList?.catalogs.find(catItem => catItem.id === p.item.id);

      if (originItem.name !== dialogResult.materialName) {
        originItem.name = dialogResult.materialName;
      }
      // 更新缩略图(客户端忽略)
      if (dialogResult.imgJson) {
        Object.entries(dialogResult.imgJson).forEach(([key, val]) => {
          if (getRelativePathFromUrl(originItem.fileInfo[key]) !== getRelativePathFromUrl(val)) {
            originItem.fileInfo[key] = val || '';
          }
        });
      }
      // originItem.fileInfo.flat = dialogResult.imgJson?.flat || '';
      // originItem.fileInfo.texture = dialogResult.imgJson?.texture || '';

      return dialogResult;
    }


    function handleRenameFolder() {
      emit('rename', p.item);
    }

    async function showRejectedReson() {
      await root.$injectDialog({
        is: DeclineRecord,
        props: {
          materialId: p.item.relateId,
        },
      });
    }

    return {
      材料规格,
      showRejectedReson,
      defaultImageSrc,
      imageSrc,
      relateType,
      elemStyles,
      material,
      costPrice,
      handleRenameFolder,
      handleShowMaterialDetail,
    };
  },
});

export default ItemFile;
