
















































































































import { defineComponent, ref } from '@vue/composition-api';
import { lf2p } from '@/filters';
import CatalogModel, { CatalogInfoMaterial, CatalogMaterial, CatalogFile } from '@/models/Catalog';
import PanelItem from '@/views/file-list/components/catalog/Item.vue';
import BaseImage from '@/components/base/Image.vue';
import isImage from '_/is-image';

const file2Icon = (content: { [x: string]: any; ext?: any; }) => {
  return content.ext;
};

const isFileElem = (elem: { [x: string]: any; type?: any; }) => {
  return elem.type === 'file';
};

const imageType2ImageKey = {
  drape: 'thumbnail' as 'thumbnail',
  flat: 'flat' as 'flat',
  texture: 'texture' as 'texture',
  default: 'texture' as 'texture' | 'thumbnail',
};

const isMaterial = (item: CatalogModel): item is CatalogModel<CatalogInfoMaterial> => {
  return item.fileType === 1 && item.relateType === 1;
};

export default defineComponent({
  name: 'Elem',
  components: {
    BaseImage,
    PanelItem,
  },
  props: {
    elem: {
      type: Object,
      required: true,
    },
    im: {
      type: Object,
      required: true,
    },
  },
  setup(props, { root: { $ImageViewer } }) {
    const panelItem = ref();

    const showFlagGlass = async ({ item, imageType }:
      { item: CatalogModel, imageType: keyof typeof imageType2ImageKey }) => {
      type VMCatalogMaterial = Omit<PanelItem, 'item'> & { item: CatalogMaterial };
      type VMCatalogFile = Omit<PanelItem, 'item'> & { item: CatalogFile };
      let vmItems = [panelItem.value];
      let items: CatalogModel[] = [panelItem.value].map(({ item: _item }) => _item);
      let srcList: string[] = [];
      if (item.relateType === 1) {
        const vmMaterialCatalogs = [panelItem.value].filter(({ item: e }) => {
          if (imageType === 'default') {
            return isMaterial(e) && e.fileInfo && (!!e.fileInfo.texture || !!e.fileInfo.thumbnail);
          }
          return isMaterial(e) && e.fileInfo && !!e.fileInfo[imageType2ImageKey[imageType]];
        }) as VMCatalogMaterial[];
        const materialCatalogs = vmMaterialCatalogs.map(({ item: e }) => e);
        vmItems = vmMaterialCatalogs;
        items = materialCatalogs;
        srcList = materialCatalogs.map(({ fileInfo }) => {
          if (imageType === 'default') {
            return fileInfo?.texture || fileInfo?.thumbnail!;
          }
          return fileInfo?.[imageType2ImageKey[imageType]]!;
        });
      } else if (item.relateType === 2) {
        const vmImageCatalogs = ([panelItem.value].filter(({ item: e }) => {
          return e.relateType === 2;
        }) as VMCatalogFile[]).filter(({ item: e }) => {
          return !!e.fileInfo?.downloadPath && isImage(e.fileInfo.downloadPath);
        });
        const imageCatalogs = vmImageCatalogs.map(({ item: e }) => e);
        vmItems = vmImageCatalogs;
        items = imageCatalogs;
        srcList = imageCatalogs.map(({ fileInfo }) => fileInfo?.downloadPath!);
      }
      if (srcList.length) {
        const targetElems = vmItems.map(({ $el }) => $el.querySelector('.img-container')!);
        const index = items.indexOf(item);
        await $ImageViewer.show(srcList, {
          index,
          targetElems,
        });
      }
    };

    return {
      panelItem,
      showFlagGlass,
    };
  },

  render() {
    let {
      elem, im, $ImageViewer, showFlagGlass,
    } = this;
    if (elem.type === 'custom') {
      elem = elem.dataObject;
    }
    if (isFileElem(elem)) {
      return <div class="elem elem-file"><a class="thumb" href={elem.url} title={elem.name} target="__blank">
        {
          elem.progress < 1
            ? <el-progress
              percentage={+((elem.progress * 100).toFixed(2))}
              show-text={false} stroke-width={4} />
            : null
        }
        <img src={file2Icon(elem)} />
        <div class="file-title">{elem.name}</div>
      </a></div>;
    }
    if (elem.type === 'image') {
      return <div class="elem elem-image">
        <div class="thumb" onClick={
          ({ target }) => $ImageViewer.show([{ src: elem.url, msrc: elem.thumb }],
            { index: 0, targetElems: [target] })}
        >
          {
            elem.progress < 1
              ? <el-progress
                percentage={+((elem.progress * 100).toFixed(2))}
                show-text={false} stroke-width={4} />
              : null
          }
          <BaseImage src={elem.thumb} style={{ width: `${elem.thumbWidth}px`, height: `${elem.thumbHeight}px` }} />
          {elem.ext === 'gif' ? <div class="file-type">{elem.ext}</div> : null}
        </div></div>;
    }
    if (elem.type === 'sound') {
      const klass = ['audio-icon', 'is-right'];
      if (elem.isPlaying) {
        klass.push('is-playing');
      }
      return <div class="elem elem-audio">
        <div class="player" onClick={() => im.playAudio(elem)} >
          {elem.second}″<div class={`audio-icon is-right ${klass}`}><div></div></div></div></div>;
    }
    if (elem.type === 'text') {
      return <div class="elem elem-text" domPropsInnerHTML={lf2p(elem.content)}></div>;
    }
    if (elem.type === 'catalog') {
      return <PanelItem
        ref="panelItem"
        class="panel-item"
        item={elem}
        shown-info={true}
        on={{
          preview: showFlagGlass,
        }} />;
    }
    return <span></span>;
  },
});
