import Jsonschema from '@4dst-saas/form-utils/dist/components/jsonschema2form/JSONschema';
import axios from '@/utils/axios';
import { fetchAntaPropertyById } from '@/loaders/dictionary';
import difference from '@4dst-saas/public-utils/dist/difference';
import customJsonschemaList from './customJsonschema';


// 获取大中小类组合的类名
async function getCategoryName(id: string) {
  return ((await axios({
    method: 'post',
    url: '/anta-material/category/name',
    data: {
      ids: [id],
    },
  })).data)[id];
}

// 获取供应商名称
async function fetchSupplierName(id: string) {
  const res = (await axios({
    method: 'post',
    url: '/anta-material/supplier/list/condition',
    data: { ids: [id] },
  })).data;
  return res?.[0]?.originalSupplierName || '';
}

export type IAttribute = {
  id?: string,
  title: string,
  required: boolean | 0 | 1,
  values?: IOption[] | undefined,
  key: string,
  isSystem?: boolean,
  component: 1 | 2 | 3 /** 小数、整数、文本是1，下拉类型是2 */,
  params?: Partial<Jsonschema>,
};

export type IOption = {
  id?: string,
  label: string,
  sort: number,
};

export function attributeValueExtractFEId(list: IAttribute['values']) {
  if (!list) return list;
  return list.map((item) => {
    const { id, label, sort } = item;
    if (id?.startsWith('@FE')) {
      return {
        label, sort,
      };
    }
    return { id, label, sort };
  });
}

export function attribute2JSONschema(itemOrList: IAttribute | IAttribute[] | {isSystem: true, key: string, title: string}) {
  const list = (
    Object.prototype.toString.call(itemOrList) === '[object Array]' ? itemOrList : [itemOrList]
  ) as IAttribute[];
  const JSONschemaList = list.map(attr => {
    if (attr.isSystem) {
      const obj = {
        ...customJsonschemaList.find(item => {
          return item.prop === attr.key;
        }),
      };

      obj.required = obj.type === 'object' ? (
        attr.required ? Object.keys(obj.properties) : []
      ) : !!attr.required;

      if (Array.isArray(obj.required) && !obj.required.length) {
        delete obj.required;
      }
      return obj;
    }
    if (!attr.params) {
      attr.params = new Jsonschema({ type: 'string' });
    }
    // 增加title
    attr.params = {
      ...attr.params,
      title: attr.title,
    };
    if (attr.key) {
      attr.params.prop = attr.key;
    }
    if (attr.required) attr.params.required = [];
    switch (attr.component) {
      case 1: return { ...attr.params! } as Jsonschema;
      case 2: {
        const oneOf = attr.values!.map(av => new Jsonschema({
          type: 'string',
          const: {
            label: av.label,
            value: av.id!,
          } as unknown as 'object',
          $id: av.id!,
        }));
        attr.params = {
          ...attr.params,
          oneOf,
          valueKey: 'value',
          labelKey: 'label',
          validator: [attr.title],
        };
        return ({ ...attr.params!, oneOf }) as Jsonschema;
      }
      case 3: {
        const anyOf = attr.values!.map(av => new Jsonschema({
          type: 'string',
          const: {
            label: av.label,
            value: av.id!,
          } as unknown as 'object',
          $id: av.id!,
        }));
        attr.params = {
          ...attr.params,
          // oneOf,
          valueKey: 'value',
          labelKey: 'label',
          validator: [attr.title],
          type: 'array',
          anyOf,
        };
        return ({ ...attr.params! }) as Jsonschema;
      }
      default: return new Jsonschema({ type: 'string' });
    }
  });
  return list.length > 1 ? JSONschemaList : JSONschemaList[0];
}

/** 将输入的JSONschema数组包成一个JSONschema (弃用) */
export function JSONschemaList2JSONschema(list: Jsonschema[], useSort = true): Jsonschema {
  const wrapper = new Jsonschema({ type: 'object', properties: {} });
  wrapper.properties = {
    ...list.reduce((pre, cur) => ({
      ...pre,
      [cur.title!]: cur,
    }), {}),
  };
  wrapper.required = [...(list.filter(item => item.required).map(item => item.title) as string[])];
  if (useSort) {
    wrapper.sort = list.map(item => item.title!);
  }
  return wrapper;
}

/**
 * 用于面料详情显示
 * 输入中类id和formData，返回转换好的formData
 *
 * @param extractList 根据权限需要去掉的属性
 */
export async function antaMiddleClassIdAndFormData2TransformedFormDataList(
  middleClassId: string,
  formData: {[key: string]: string | number| object},
  extractList: string[] = [],
) {
  const properties = (await axios({
    url: '/anta-material/template/middle',
    params: { id: middleClassId },
    method: 'GET',
  })).data as { propertyId?: string, sort: number, isSystem: boolean, title: string, key: string }[];

  if (!properties.length) return formData;

  // 排序
  const propertiesSortList = properties.sort(
    (a, b) => Math.abs(a.sort) - Math.abs(b.sort),
  );

  // 处理一下
  const list = (await Promise.all(propertiesSortList.map(async (propertyItem) => {
    if (propertyItem.isSystem) {
      return propertyItem;
    }
    return (await axios({
      url: `/anta-material/dynamics/property/detail?id=${propertyItem.propertyId}`, method: 'GET',
    })).data as IAttribute;
  }))).filter(i => extractList.indexOf(i.title) === -1);


  function setColorLimitType(colorLimitType : 1 | 2 | 3 | undefined) {
    switch (colorLimitType) {
      case 1: return '全部颜色';
      case 2: return '仅部分颜色';
      case 3: return '荧光色除外';
      default: return '--';
    }
  }

  // 将值转为对应显示的东西
  const l = await Promise.all(list.map(async (item) => {
    if (item.isSystem) {
      switch (item.key) {
        case 'category': return { label: item.title, value: await getCategoryName(formData[item.key] as string) };
        case 'season': return {
          label: item.title,
          value: formData[item.key]
            ? `${formData[item.key].year || ''}${formData[item.key].season || ''}${await fetchAntaPropertyById(formData[item.key]['品牌'])}`
            : '--',
        };
        case 'reference_price': {
          const referenceUnit = formData.reference_price.unit === 1 ? '人民币' : '美元';
          return { label: item.title, value: formData[item.key].value ? `${formData[item.key].value}${referenceUnit}` : '--' };
        }
        case 'cost': {
          const costUnit = formData.cost.unit === 1 ? '人民币' : '美元';
          return { label: item.title, value: formData[item.key].value ? `${formData[item.key].value}${costUnit}` : '--' };
        }
        case 'color_limit_type': return { label: item.title, value: setColorLimitType(formData[item.key] as 1 | 2 | 3) };
        case 'evergreen': return { label: item.title, value: formData[item.key] === 1 ? 'TRUE' : 'FALSE' };
        case 'supplier_name': return { label: item.title, value: await fetchSupplierName(formData[item.key] as string) };
        case 'materialName':
        case 'material_number': return { label: item.title, value: formData[item.key] };
        // default: throw new Error(`数据转换出错。当前key为${item.key}`);
        default: throw new Error(`数据转换出错。当前title为${item.title}`);
      }
    }

    // 非系统属性
    switch (item.component) {
      case 1: return { label: item.title, value: formData[item.title] };
      // case 3: return { label: item.title, value: formData[item.id!] };
      case 2: {
        // 先判断是否在可选值中。如果在可选值中，直接获取可选值，否则还要根据id调接口获得
        const wait4FetchValue = formData[item.title] as string;
        const target = item.values!.find(i => i.id === wait4FetchValue);
        if (target) return { label: item.title, value: target.label };
        const label = (await axios({
          url: '/anta-material/dynamics/property/value/id',
          method: 'GET',
          params: { id: wait4FetchValue },
        })).data as string;
        return { label: item.title, value: label };
      }
      case 3: {
        const wait4FetchValue = formData[item.title] as string[] || [];
        const notFounds = wait4FetchValue.filter(v => !item.values!.find(i => i.id === v));
        // if (!notFounds.length) return { label: item.title, value: targets.join(',') };
        const labels = await Promise.all(notFounds.map(async (id) => (await axios({
          url: '/anta-material/dynamics/property/value/id',
          method: 'GET',
          params: { id },
        })).data as string));
        const res = labels.concat(
          difference(wait4FetchValue, notFounds).map(
            v => item.values.find(i => i.id === v)?.label,
          ),
        ).join(',');
        // return res;
        return { label: item.title, value: res };
      }
      default: throw new Error(`数据转换出错。当前component为${item.component}`);
    }
  }));
  return l;
}

/** 输入中类id和formData，返回转换好的formData(弃用) */
export async function antaMiddleClassIdAndFormData2TransformedFormData(
  middleClassId: string,
  formData: {[key: string]: string | number},
) {
  const properties = (await axios({
    url: '/anta-material/template/middle',
    params: { id: middleClassId },
    method: 'GET',
  })).data as { propertyId: string }[];
  if (!properties.length) return formData;
  const pIds = properties.map(property => property.propertyId);

  const list = await Promise.all(pIds.map(async (id) => {
    return (await axios({
      url: `/anta-material/dynamics/property/detail?id=${id}`, method: 'GET',
    })).data as IAttribute;
  }));
  const res = { ...formData };
  const filterList = list.filter(attr => attr.component === 2);
  const shouldFetchList:{key: string, id: string}[] = [];
  filterList.forEach(item => {
    const key = item.title;
    const wait4FetchValue = formData[key] as string;
    const target = item.values!.find(i => i.id === wait4FetchValue);
    // 找到了
    if (target) res[key] = target.label;
    else shouldFetchList.push({ key, id: wait4FetchValue });
  });
  if (shouldFetchList.length) {
    await Promise.all(shouldFetchList.map(async ({ id, key }) => {
      const value = (await axios({
        url: '/anta-material/dynamics/property/value/id',
        method: 'GET',
        params: { id },
      })).data as string;
      console.log(value);
      res[key] = value;
    }));
  }
  return res;
}

/** 输入jsonschema，返回包含检验下拉框值的validator函数组成的对象 */
export function jsonschema2FormItemValidatorObject(jss: Jsonschema) {
  if (jss.type !== 'object') throw new Error('输入的jsonschema不是object类型');
  if (!jss.properties) throw new Error('输入的jsonschema没有properties');
  const validatorObject:{[key: string]: Function} = {};
  const validatorKeys = Object.keys(jss.properties!).filter(key => jss.properties![key].oneOf);
  validatorKeys.forEach(key => {
    if (jss.properties![key].type === 'array') return;
    if (jss.required?.includes(key) || jss.properties![key].required
    || (typeof (jss.properties![key].disabled) === 'boolean' && !jss.properties![key].disabled)) {
      validatorObject[key] = (value: string) => {
        const valueList = (jss.properties![key].oneOf!.map(item => item.const! as unknown as {
          label: string, value: string,
        })).map(({ value: _v }) => _v);
        console.log(valueList);
        if (!valueList.includes(value)) throw new Error('此值已被删除');
      };
    }
  });
  return validatorObject;
}
