









































































































import {
  defineComponent, ref, computed,
} from '@vue/composition-api';
import i18n from 'i18next';
import axios from '@/utils/axios';
// import tryParseJson from '@/utils/try-parse-json';
import { getUsersInfoByIds } from '@/api/account';
// import { fetchSupplierAll } from '@/loaders/supplier';
import useLoader from '_/composables/use-loader';
import isQt from '@/utils/is-qt';
import { getHost } from '@/utils/exec/index';
import { getContextUser } from '@/loaders/context';
import envs from '@/utils/envs';

import { handleUploadMaterial4ddat } from '@/utils/catalog/utils';
import ComponentPreviewDetail from './components/ComponentPreviewDetail.vue';
import ComponentEditDetail from './components/ComponentEditDetail.vue';
import MaterialCarousel from './components/MaterialCarousel.vue';
import TimeInfo from './components/TimeInfo.vue';
import MaterialOperateHistory from './components/MaterialOperateHistory/index.vue';
import DeclineRecord from './components/DeclineRecord.vue';

/**
 * 获取材质详情信息
 * @param {String} materialId 材质Id
 * @see http://yapi.4dshoetech.local/project/115/interface/api/6026
 */
export const getMaterialInfo = (materialId: string, language = 'cn', groupAppId?: string) => {
  return axios({
    method: 'get',
    url: '/anta-material/material/management/getMaterialBasicInfoAndTags',
    headers: {
      language,
      groupAppId,
    },
    params: { materialId },
  });
};

// 获取中类categoryId
async function fetchMiddleCategoryId(id: string) {
  const ids = (await axios({
    method: 'GET',
    url: '/anta-material/category/parents',
    params: { id },
  })).data as string[];
  return ids[1];
}


export default defineComponent({
  name: 'MaterialInfo',
  dialogify: {
    props: {
      top: '5vh',
      width: '900px',
      noPadding: true,
      closeOnClickModal: false,
      // compact: true,
    },
    closeWith: 'material',
  },
  components: {
    ComponentPreviewDetail,
    ComponentEditDetail,
    MaterialCarousel,
    TimeInfo,
    MaterialOperateHistory,
    DeclineRecord,
  },
  props: {
    groupAppId: {
      type: String,
    },
    id: {
      type: String,
      required: true,
    },
    entryAppType: {
      type: Number,
    },
    editable: {
      type: Boolean,
      default: true,
    },
    thumbIndex: {
      type: Number,
      default: 0,
    },
  },
  setup(props, { root }) {
    const { $errHandler } = root;
    const currentComponent = ref('ComponentPreviewDetail');
    const saveBtnDisabled = ref(true);
    const loading = ref(false);
    const material = ref<any>({
      imgJson: '',
      creatorName: '',
      gmtCreateTime: '',
      gmtModifyTime: '',
      creator: '',
      extraProperty: {
        category: '',
        season: {},
      },
    });
    const isCreator = computed(() => getContextUser()?.id === material.value.creator);

    // 中类ID
    const middleCategoryId = useLoader(async ({ value: { extraProperty: { category } } }) => {
      // eslint-disable-next-line prefer-rest-params
      if (!category) return '';
      return fetchMiddleCategoryId(category);
    }, material, {
      initialValue: null,
      loadingValue: () => null,
    });

    const materialDynamicComp = ref<InstanceType<typeof ComponentEditDetail>>();

    const swipers = ref<any>([]);
    const title = ref(i18n.t('file-list.catalog.material_info'));
    const isHistoryComponent = computed(() => currentComponent.value === 'MaterialOperateHistory');
    // const isHistoryComponent = computed(() => false);
    const contentStyle = computed(() => (isHistoryComponent.value ? 'is-higher' : ''));
    // const contentStyle = computed(() => ({
    //   // 切换到操作记录时，隐藏了按钮行，为保证高度一样，增加按钮行的高度
    //   height: isHistoryComponent.value ? '612px' : '550px',
    // }));

    async function updateMaterial() {
      await materialDynamicComp.value!.save();
    }

    // 获取轮播的需要的数据结构
    function _getSwiperObj(mat: any) {
      if (!mat.imgJson) return [];
      swipers.value = [];
      // 获取三张合成图
      // ['thumbnail', 'flat', 'toeThumbnail'].forEach(item => {
      ['thumbnail', 'flat', 'texture'].forEach(item => {
        // @ts-ignore
        const img = mat.imgJson[item];
        if (img) swipers.value.push({ type: 'image', url: img, imageType: item });
      });

      if (!mat.assetsInfo) return swipers.value;
      // 获取附件的材质图片
      // @ts-ignore
      swipers.value.push(...mat.assetsInfo?.images.map(item => {
        return { type: 'image', url: item };
      }));

      // 获取附件的视频
      // @ts-ignore
      swipers.value.push(...mat.assetsInfo?.videos.map(item => {
        return { type: 'video', url: item };
      }));

      //  获取附件的模型
      // @ts-ignore
      swipers.value.push(...mat.assetsInfo?.models.map(item => {
        return { type: 'model', url: item.cover, id: item.id };
      }));
      return swipers.value;
    }

    // 获取模型信息及捕获异常操作
    async function getMaterialInfoById(id: string) {
      try {
        const materialOrigin = (
          await getMaterialInfo(id, i18n.language, props.groupAppId)
        ).data;
        // 处理单位类型的数据类型 number 转为 string; (否则编辑页面的表单校验会提示转为字符串类型)
        Object.keys(materialOrigin).forEach(key => {
          if (key === 'imgJson') return;
          if (materialOrigin[key] === null) materialOrigin[key] = '';
          else if (typeof materialOrigin[key] === 'number') materialOrigin[key] = materialOrigin[key].toString();
        });

        // 根据用户id，获取用户信息
        if (materialOrigin.modifier === materialOrigin.creator) {
          const userName = (await getUsersInfoByIds([materialOrigin?.creator])).data[0]?.name;
          materialOrigin.creatorName = userName;
          materialOrigin.modifierName = userName;
        } else {
          const userNames = (await getUsersInfoByIds([materialOrigin?.creator, materialOrigin?.modifier])).data;
          materialOrigin.creatorName = userNames[0].name;
          materialOrigin.modifierName = userNames[1].name;
        }
        material.value = materialOrigin;

        _getSwiperObj(material.value);
      } catch (err) {
        $errHandler(err);
      }
    }

    // 跳转到变更记录
    function toOperateHistory() {
      currentComponent.value = 'MaterialOperateHistory';
      title.value = material.value.materialName;
    }

    // 返回面料详情
    function backOperate() {
      currentComponent.value = 'ComponentPreviewDetail';
      title.value = i18n.t('file-list.catalog.material_info');
    }

    // 弹出驳回记录
    async function showDeclineRecord(materialId: string) {
      await root.$injectDialog({
        is: DeclineRecord,
        props: {
          materialId,
        },
      });
    }
    async function printAsPDF() {
      const routeUrl = root.$router.resolve({
        path: '/material-info',
        query: {
          groupAppId: props.groupAppId,
          id: props.id,
          entryAppType: props.entryAppType?.toString(),
          editable: 'false',
        },
      });
      if (isQt()) {
        const nativeHost = await getHost();
        nativeHost.openWin({
          url: routeUrl.href,
        });
      } else {
        window.open(routeUrl.href, '_blank');
      }
    }

    async function handleUpload4ddat() {
      await handleUploadMaterial4ddat({
        workspaceId: props.groupAppId,
        relateId: props.id,
      });
      console.info('执行刷新');
      await getMaterialInfoById(props.id);
    }

    getMaterialInfoById(props.id);

    return {
      material,
      materialDynamicComp,
      updateMaterial,
      swipers,
      currentComponent,
      saveBtnDisabled,
      loading,
      getMaterialInfoById,
      title,
      toOperateHistory,
      backOperate,
      showDeclineRecord,
      printAsPDF,
      contentStyle,
      isHistoryComponent,
      middleCategoryId,
      isQt,
      handleUpload4ddat,
      isCreator,
      envs,
    };
  },
});
