
import { defineComponent, onMounted, ref } from '@vue/composition-api';
// import axios from '@/utils/axios';
import { fetchSupplierAll } from '@/loaders/supplier';

const SupplierSelect = defineComponent({
  name: 'SupplierSelect',
  props: {
    supplier_name: String,
  },
  model: {
    prop: 'supplier_name',
    event: 'change',
  },
  setup() {
    const supplierList = ref([]);
    onMounted(async () => {
      supplierList.value = await fetchSupplierAll();
    });
    return {
      supplierList,
    };
  },

  render() {
    return <el-select { ...{ attrs: this.$attrs.attrs } } value={this.supplier_name} onchange={ (e: string) => this.$emit('change', e) }>
    {/* // return <el-select vModel={this.supplier_name} { ...this.$attrs.attrs }> */}
      {this.supplierList.map((s: {id: string, name: string, originalSupplierName: string}) => {
        return <el-option value={s.id} label={ s.originalSupplierName }></el-option>;
      })}
    </el-select>;
  },
});

export default SupplierSelect;
