

































































/* eslint-disable camelcase */

import {
  computed, defineComponent, ref,
} from '@vue/composition-api';
import {
  getDictionaryByTypeCode,
  fetchAntaPropertyByCode,
} from '@/loaders/dictionary';


import { DictItem } from '@/models/Dictionary';
import keys from '_/keys';
import axios from '@/api/axios';
import { getCurrentCompany } from '@/loaders/company';
import useLoader from '_/composables/use-loader';
import SupplierPriceList from '../SupplierPriceList.vue';
import {
  fetchProperties,
  findValue,
  getProperty,
  getPropertyByTitle,
  getSelectionProperty,
} from './property';
import materialTypes from './materialType';

export type PriceUnit = {
  label: string,
  // itemCode: string,
  itemValue: number,
};

async function fetchSupplierAll() {
  const res = (await axios({
    method: 'post',
    url: '/anta-material/supplier/list/condition',
    data: {},
  })).data;
  return res;
}

const colorList: { [key: number]: string } = {
  1: '全部颜色',
  2: '仅部分颜色',
  3: '荧光色除外',
};

const approveStatus = {
  1: '待评审',
  2: '评审中',
  3: '复核中',
  4: '已完成',
} as { [x: number]: string };

type SupplierPrice = {
  reference_price_unit: number,
  reference_price_value: number,
  cost_unit: number,
  cost_value: number,
  supplier_name: string,
  supplier_status: number
};

type Message = {
  label: string;
  oldValue: string;
  newValue: string;
};

type PriceType = {
  value: string | number;
  unit: number;
};

type Supplier = {
  id: string;
  originalSupplierName: string;
  companyId?: string;
  name?: string;
};

type Dictionary = {
  id: string;
  value: string;
};

type TypeItem = {
  itemCode: string;
  itemValue: string;
  label: string;
};
export default defineComponent({
  props: {
    form: {
      required: true,
    },
  },
  components: {
    SupplierPriceList,
  },
  setup(props) {
    const changeLogRef = ref();
    const isSupplier = computed(() => {
      return getCurrentCompany()?.type === 3;
    });
    let formList;
    if (typeof props.form === 'string') {
      formList = JSON.parse(JSON.parse(props.form));
    } else {
      formList = props.form;
    }
    // const formList = JSON.parse(props.form);
    const oldForm = formList[1];
    const newForm = formList[0];

    const itemName = newForm.materialName || '';
    const showAll = ref(false);
    const showSupplierPrice = ref(false);
    // const statusList = ['--', '开发中', '已完成', '驳回', '停用'];
    // const typeItemList = ['priceUnit', 'mqqUnit'];
    const evergreenList = ['', 'TRUE', 'FALSE'];

    const isSidebar = computed(() => {
      if (changeLogRef.value) {
        console.log(changeLogRef.value.offsetWidth);
        return changeLogRef.value.offsetWidth < 440;
      }
      return false;
    });


    function isDifferent(a: any, b: any) {
      return JSON.stringify(a) !== JSON.stringify(b);
    }

    function findTypeItem(list: PriceUnit[], value: number | undefined) {
      return (
        list.find((i) => {
          return i.itemValue === value;
        })?.label || '--'
      );
    }

    function handleSupplierName(id: string, list: Supplier[]) {
      return (
        list.find((supplier) => {
          return supplier.id === id;
        })?.originalSupplierName || '--'
      );
    }

    function handleCostLikeData(
      data: PriceType | undefined,
      priceUnitList: PriceUnit[],
    ) {
      // console.log('handleCostList', data, priceUnitList);
      if (data && data.value) {
        return `${data.value ?? '--'} ${findTypeItem(
          priceUnitList,
          data.unit,
        )}`;
      }
      return '--';
    }

    function fetchSupplierPriceArr(
      current: SupplierPrice,
      key: 'reference_price'
    ): PriceType;
    function fetchSupplierPriceArr(
      current: SupplierPrice,
      key: 'cost'
    ): PriceType;
    function fetchSupplierPriceArr(
      current: SupplierPrice,
      key: 'supplier_name'
    ): string;
    function fetchSupplierPriceArr(
      current: SupplierPrice,
      key: 'reference_price' | 'cost' | 'supplier_name',
    ) {
      if (!current) return undefined;
      // const current = prices[0];
      switch (key) {
        case 'reference_price':
          return {
            value: current.reference_price_value,
            unit: current.reference_price_unit,
          } as PriceType;
        case 'cost':
          return {
            value: current.cost_value,
            unit: current.cost_unit,
          } as PriceType;
        case 'supplier_name':
          return current.supplier_name as string;
        default:
          return undefined;
      }
    }

    function handleSupplierPriceArr(
      list: SupplierPrice[],
      priceUnitList: PriceUnit[],
      supplierList: Supplier[],
    ) {
      return list.map((i) => {
        return {
          referencePrice: handleCostLikeData(
            fetchSupplierPriceArr(i, 'reference_price'),
            priceUnitList,
          ),
          cost: handleCostLikeData(
            fetchSupplierPriceArr(i, 'cost'),
            priceUnitList,
          ),
          supplier_name: handleSupplierName(
            fetchSupplierPriceArr(i, 'supplier_name'),
            supplierList,
          ),
          supplierStatus: i.supplier_status,
        };
      });
    }

    function getPriceUnit() {
      return [{
        label: '人民币',
        // itemCode: 'CNY',
        itemValue: 1,
      }, {
        label: '美元',
        // itemCode: 'CNY',
        itemValue: 2,
      }];
    }

    const messageList = useLoader(
      async () => {
        const oldKeys = keys(oldForm);
        const newKeys = keys(newForm);
        const form = oldKeys.length > newKeys.length ? oldForm : newForm;

        const [
          allSupplierList,
          priceUnits,
          propertyList,
        ] = await Promise.all([
          fetchSupplierAll(),
          getPriceUnit(),
          fetchProperties(),
        ]);

        console.log(propertyList);

        const messages = keys(form).map((key) => {
          let oldValue;
          let newValue;
          let type = 0;
          let label = materialTypes[(key as unknown) as keyof typeof materialTypes];
          if (isDifferent(oldForm[key], newForm[key])) {
            switch (key) {
              case 'evergreen':
                oldValue = evergreenList[Number(oldForm[key])] || '--';
                newValue = evergreenList[Number(newForm[key])] || '--';
                break;
              case 'materialName':
                if (oldForm[key] !== newForm[key]) {
                  oldValue = oldForm[key];
                  newValue = newForm[key];
                }
                break;
              case 'material_status':
                // oldValue = statusList[oldForm[key]];
                // newValue = statusList[newForm[key]];
                oldValue = undefined;
                newValue = undefined;
                break;
              case 'color_limit_type':
                oldValue = colorList[oldForm[key]] || '--';
                newValue = colorList[newForm[key]] || '--';
                break;
              case 'approve_detail':
                oldValue = approveStatus[oldForm[key].approve_status] || ' -- ';
                newValue = approveStatus[newForm[key].approve_status] || ' -- ';
                break;
              case 'season':
                // eslint-disable-next-line max-len
                oldValue = `${oldForm.season?.year || ''} ${oldForm.season?.season || ''} ${findValue(oldForm.season['品牌'], getSelectionProperty('品牌')) || oldForm.season['品牌']}`;
                // eslint-disable-next-line max-len
                newValue = `${newForm.season?.year || ''} ${newForm.season?.season || ''} ${findValue(newForm.season['品牌'], getSelectionProperty('品牌')) || newForm.season['品牌']}`;
                break;
              case 'supplier_price_array':
                oldValue = handleSupplierPriceArr(
                  oldForm[key],
                  priceUnits,
                  allSupplierList,
                );
                newValue = handleSupplierPriceArr(
                  newForm[key],
                  priceUnits,
                  allSupplierList,
                );
                type = 1;
                break;
              default:
                console.log(key);
                console.log(oldForm[key], newForm[key]);
                oldValue = findValue(
                  oldForm[key],
                  getSelectionProperty(key as string),
                );
                newValue = findValue(
                  newForm[key],
                  getSelectionProperty(key as string),
                );
                console.log(oldValue, newValue);
                label = label ?? key as string;
                break;
            }
          }

          return {
            label,
            oldValue,
            newValue,
            type,
          };
        });
        console.info(messages);
        // debugger;
        return messages.filter((item) => {
          // console.log(item);
          return item.oldValue !== undefined && item.newValue !== undefined;
        });
      },
      {},
      {
        initialValue: undefined,
      },
    );

    return {
      messageList,
      itemName,
      oldForm,
      newForm,
      showAll,
      showSupplierPrice,
      changeLogRef,
      isSidebar,
      isSupplier,
      // fetchCategory,
    };
  },
});
