/**
 * 生成冒泡的事件
 * @param {string[] | string} eventTypes
 * @this {Vue | null | undefined}
 */
export function bubbleUpEvents(eventTypes) {
  const that = this;
  if (typeof eventTypes === 'string') {
    eventTypes = [eventTypes];
  }
  return eventTypes.reduce((memo, type) => {
    memo[type] = function bubbleUpEvent(...args) {
      return (this || that).$emit(type, ...args);
    };
    return memo;
  }, {});
}
export default bubbleUpEvents;
