<template>
  <div>
    <History
      v-if="session"
      id="1209646224555249666"
      style="width: 500px; height: 500px; position: relative"></History>
    <textarea v-model="text"></textarea>
    <button @click="send">发送</button>
    <input type="file" @change="selectFile" accept="image/*" />
  </div>
</template>
<script>
// import api from '@/api/axios';
import History from '@/components/im/History';

export default {
  components: {
    History,
  },
  data() {
    return {
      text: '',
      session: undefined,
    };
  },
  async created() {
    // const groups = (await api.get('im/chat/info/groups')).data;
    this.session = await this.$im.getSession({ id: '1209646224555249666', msgCount: 15 });
  },
  methods: {
    async selectFile(e) {
      const file = e.target.files[0];
      if (file) {
        await this.$im.send(this.session, file);
      }
    },
    async send() {
      await this.$im.send(this.session, this.text);
    },
  },

};
</script>
