
import {
  defineComponent, ref, set, computed,
} from '@vue/composition-api';
import useLoader from '_/composables/use-loader';
import axios from '@/api/axios';
import NumberType from '@/components/dynamic-form/jsonschema2form/NumberType';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import { validateForm } from '@/utils/validators/validate-form';
import { isFilled } from '@/utils/validators/is-filled';
import strLength from '@/utils/validators/str-length';
import {
  normalizeRendererImgUrl, getRelativePathFromUrl,
} from '@/utils/normalize-component';

import { DictItem } from '@/models/Dictionary';
import { ElForm } from 'element-ui/types/form';
import BaseImage from '@/components/base/Image.vue';
import TagsEditor from './TagsEditor';

type IClothesDetail = {
  creator: string;
  curtailImg: string;
  gmtCreateTime: number;
  gmtModifyTime: number;
  id: string;
  modelNumber: string;
  modifier: string;
  price: number;
  priceUnit: string;
  priceUnitObject: any;
  stylistic: string;
  synopsis: string;
  tagsInfo: { id: string, tag: string }[];
  type: number | string;
  typeObject: any;
};

interface IMaterialItem {
  id: string,
  materialName: string,
  materialID: string,
  renderArguments: string,
  color: string,
  supplierArticleName: string,
  samplePrice: number,
  samplePriceUnit: number,
  supplierName: string,
  imgJson: { [key: string]: string },
}

interface CtmItem {
  additionalJson: any,
  additionalJsonExt: null | {
    material?: Object, decalInfo?: Array<{
      angle: number,
      map: string,
      position: number[],
      rotation: number[],
      scale: number,
    }>
  },
  ctmId: string
  ctmName: string
  ctmPath: string
  material: any
  materialId: string
  modelComponentId: string
  modelFileId: string
  modelId: string
}

interface Hash {
  [key: string]: any
}

// eslint-disable-next-line no-shadow
enum IMode {
  view,
  edit
}

function preProcess(target: IClothesDetail) {
  if (target.type === 0) {
    target.type = '';
  }
  if (!target.price) {
    set(target, 'price', '');
  } else {
    set(target, 'price', `${target.price}`);
  }
}

async function getClothesDetailInfo(clothingId: string) {
  const res = (await axios({
    method: 'GET',
    url: '/bff-center/showCommon/clothingInfoSearch/getClothingInfoAndTags',
    params: { clothingId },
  })).data;
  if (res) preProcess(res);
  return res as IClothesDetail || null;
}

async function getMaterialsInfo(modelId: string) {
  const res = (await axios({
    method: 'GET',
    url: '/show/application/model/materials',
    params: { modelId },
    headers: {
      'requester-type': 'web',
      'model-type': 'CLOTHES',
    },
  })).data;
  return res as IMaterialItem[] || null;
}

async function getPatternsInfo(modelId: string) {
  const res = (await axios({
    method: 'GET',
    url: '/show/application/model/ctms',
    params: { modelId },
    headers: {
      'requester-type': 'web',
      'model-type': 'CLOTHES',
    },
  })).data as CtmItem[];
  let patterns: string[] = [];
  res.forEach(item => {
    const decals = item.additionalJsonExt?.decalInfo || item.additionalJson?.decalInfo;
    if (decals) {
      patterns.push(...(decals.map(decal => decal.map)));
    }
  });
  patterns = Array.from(new Set(patterns));
  const patternUrls = await Promise.all(patterns.map(async pattern => {
    // let imgUrl = getRelativePathFromUrl(pattern);
    const imgUrl = await normalizeRendererImgUrl(pattern);
    return imgUrl;
  }));
  return patternUrls;
}

const rules = {
  modelNumber: validator2ElemRule((val: string) => {
    isFilled(val);
    strLength(val, {
      min: 1, max: 32,
    });
  }),
  type: validator2ElemRule(isFilled),
  synopsis: validator2ElemRule((val: string) => {
    if (val && val.length > 300) {
      throw new Error('简介长度要小于300个字符');
    }
  }),
  price: validator2ElemRule((val: string | number) => {
    if (val && String(val).length > 8) {
      throw new Error('请输入8位数以下的数字');
    }
  }),
};

const cDetail = defineComponent({
  dialogify: {
    props: {
      width: '600px',
      title: '简介',
    },
    closeWith: 'detailInfo',
  },
  components: {
    BaseImage,
  },
  props: {
    clothingId: {
      type: String,
      required: true,
    },
    isDialog: {
      type: Boolean,
      default: false,
    },
  },
  render() {
    const s = this as InstanceType<typeof cDetail>;
    const { detailInfo } = s;

    let viewMode = null;
    let editMode = null;
    if (detailInfo) {
      viewMode = <div class='view-mode-container'>
        <section class='item-container'>
          <div class="item-title margin-bottom-8">标签</div>

          <div class='tags-container'>
            {
              detailInfo?.tagsInfo?.length > 0
                ? <TagsEditor tags={s.detailInfo?.tagsInfo || []} type='4' relateType='4'
                  key='view' shareId={s.clothingId} editable={false} />
                : '- -'
            }
          </div>
        </section>

        <section class="item-container flex-column">
          <div class="item-title margin-bottom-8">款式名称</div>

          <div class="item-content">{detailInfo!.modelNumber}</div>
        </section>

        <section class="item-container flex-column">
          <div class="item-title margin-bottom-8">款式类型</div>

          <div class="item-content">{
            s.selectOptions
              ? s.selectOptions.find(i => i.itemValue === detailInfo?.type)?.label
              : null
          }</div>
        </section>

        <section class="item-container flex-column">
          <div class="item-title margin-bottom-8">价格</div>

          <div class="item-content">{detailInfo!.price} {
            s.priceOptions
              ? s.priceOptions.find(i => i.itemValue === detailInfo?.priceUnit)?.label
              : null
          }</div>
        </section>

        <section class="item-container">
          <div class="item-title margin-bottom-8">简介</div>

          <div class="item-content item-info" style={{
            wordBreak: 'break-word',
          }}>{detailInfo?.synopsis || '- -'}</div>
        </section>

        <section class="item-container">
          <div class="item-title margin-bottom-8">面料</div>

          {
            s.filteredMaterialInfo
              ? <ul class="materials">
                {
                  s.filteredMaterialInfo.map(item => {
                    return (
                      <el-tooltip effect="dark" content={item.materialName} placement="bottom">
                        <li class="material">
                          <img class="material-img" src={item.imgJson.flat} />
                        </li>
                      </el-tooltip>
                    );
                  })
                }
              </ul>
              : <div class="item-content">- -</div>
          }
        </section>

        <section class="item-container">
          <div class="item-title margin-bottom-8">工艺图案</div>

          {
            s.patternsInfo
              ? <ul class="materials">
                {
                  s.patternsInfo.map(item => {
                    return (
                      <li class="material">
                        <BaseImage class="material-img" src={item} fit="contain" />
                      </li>
                    );
                  })
                }
              </ul>
              : <div class="item-content">- -</div>
          }
        </section>

        <footer
          class={['flex-justify-flex-end', s.isDialog ? '' : 'detail-footer']}>
          <el-button class="button-type" type='primary' onclick={() => {
            s.oldInfo = JSON.parse(JSON.stringify(s.detailInfo));
            s.mode = IMode.edit;
          }}>编辑</el-button>
        </footer>
      </div>;

      editMode = <div class='edit-mode-container'>
        <section class='item-container'>
          <div class="item-title">标签</div>

          <div class='tags-container'>
            {
              <TagsEditor tags={s.detailInfo?.tagsInfo || []} type='4' relateType='4'
                key='view' shareId={s.clothingId} editable={true} />
            }
          </div>
        </section>

        <el-form ref='detailForm' props={{ model: detailInfo }} rules={rules}>
          <el-form-item class="item-title" prop='modelNumber' label='款式名称'>
            <el-input vModel={detailInfo!.modelNumber}></el-input>
          </el-form-item>

          <el-form-item class="flex-column" prop='type' label='款式类型' style={{
            alignItems: 'start',
          }}>
            <el-select vModel={detailInfo!.type}>
              {
                s.selectOptions.map(option => <el-option
                  key={option.itemCode} value={option.itemValue}
                  label={option.label}></el-option>)
              }
            </el-select>
          </el-form-item>

          {
            <el-form-item class="flex-column content-width-100" prop='price' label='价格' style={{
              alignItems: 'start',
            }}>
              <div class="flex">
                <NumberType builder='el-input' style={{
                  width: '70%',
                }} max={99999999} vModel={detailInfo!.price}></NumberType>

                <el-select style={{
                  flex: 1,
                }} vModel={detailInfo!.priceUnit}>
                  {
                    s.priceOptions.map(option => <el-option
                      key={option.itemCode} value={option.itemValue}
                      label={option.label}></el-option>)
                  }
                </el-select>
              </div>
            </el-form-item>
          }

          <el-form-item class="item-title" prop='synopsis' label='简介'>
            <el-input rows='5' type='textarea' maxlength='300'
              vModel={detailInfo!.synopsis}></el-input>
          </el-form-item>

        </el-form>

        <section class="item-container">
          <div class="item-title margin-bottom-8">面料</div>
          {
            s.filteredMaterialInfo
              ? <ul class="materials">
                {
                  s.filteredMaterialInfo.map(item => {
                    return (
                      <el-tooltip effect="dark" content={item.materialName} placement="bottom">
                        <li class="material">
                          <img class="material-img" src={item.imgJson.flat} />
                        </li>
                      </el-tooltip>
                    );
                  })
                }
              </ul>
              : <div class="item-content">- -</div>
          }
        </section>

        <section class="item-container">
          <div class="item-title margin-bottom-8">工艺图案</div>

          {
            s.patternsInfo
              ? <ul class="materials">
                {
                  s.patternsInfo.map(item => {
                    return (
                      <li class="material">
                        <BaseImage class="material-img" src={item} fit="contain" />
                      </li>
                    );
                  })
                }
              </ul>
              : <div class="item-content">- -</div>
          }
        </section>

        <footer
          class={['flex-justify-flex-end', s.isDialog ? '' : 'detail-footer']}>
          <el-button class="button-type" onclick={() => {
            s.detailInfo = s.oldInfo!;
            s.mode = IMode.view;
          }}>取消</el-button>

          <el-button class="button-type" type='primary'
            onclick={() => s.handleAfterEdit.call(s)}>确定</el-button>
        </footer>
      </div>;
    }
    return <section vLoading={!s.detailInfo}
      class={['clothing-detail-container flex-column scrollable-y', s.isDialog ? '' : 'padding-20']}
    >
      {
        <h3 class="clothing-detail-title">简介</h3>
      }
      {
        // eslint-disable-next-line
        !detailInfo
          // eslint-disable-next-line
          ? null :
          s.mode === IMode.view
            ? viewMode
            : editMode
      }
    </section>;
  },
  setup(props) {
    const mode = ref(IMode.view);
    const detailInfo = useLoader(async ({ clothingId }) => {
      if (!clothingId) return null;
      return getClothesDetailInfo(clothingId);
    }, props, {
      initialValue: null,
    });
    const oldInfo = ref<IClothesDetail>();


    const materialInfo = useLoader(async ({ clothingId }) => {
      if (!clothingId) return null;
      return getMaterialsInfo(clothingId);
    }, props, {
      initialValue: null,
    });

    const patternsInfo = useLoader(async ({ clothingId }) => {
      if (!clothingId) return null;
      return getPatternsInfo(clothingId);
    }, props, {
      initialValue: null,
    });

    // 重复面料去重
    const filteredMaterialInfo = computed(() => {
      if (!materialInfo.value || materialInfo.value.length === 0) return [];
      const hash: Hash = {};
      return materialInfo.value!.filter(item => {
        if (!hash[item.id]) {
          hash[item.id] = item;
          return true;
        }
        return false;
      });
    });

    const selectOptions = useLoader(async () => {
      const res = (await axios({
        url: '/bff-center/showCommon/getDictionaryTypeItem',
        headers: {
          language: 'cn',
        },
        params: {
          typeCode: 'clothingType',
        },
      })).data;
      return res as DictItem[];
    }, {}, {
      initialValue: [],
    });

    // 去除不必变更的字段
    function getEditableProperties(t = detailInfo.value) {
      return {
        ...t,
        curtailImg: undefined,
      };
    }

    const detailForm = ref<ElForm>();
    async function handleAfterEdit() {
      if (detailForm.value) {
        await validateForm(detailForm.value);
        await axios({
          url: '/bff-center/showCommon/clothingInfoOperation/updateClothingInfo',
          method: 'POST',
          data: {
            ...getEditableProperties(),
          },
        });
        mode.value = IMode.view;
      }
      // emit('confirm', detailInfo.value);
    }

    const priceOptions = useLoader(async () => {
      const res = (await axios({
        url: '/bff-center/showCommon/getDictionaryTypeItem',
        headers: {
          language: 'cn',
        },
        params: {
          typeCode: 'priceUnit',
        },
      })).data;
      return res as DictItem[];
    }, {}, {
      initialValue: [],
    });

    return {
      oldInfo,
      detailForm,
      mode,
      selectOptions,
      priceOptions,
      detailInfo,
      filteredMaterialInfo,
      patternsInfo,

      handleAfterEdit,
    };
  },
});
export default cDetail;
