




































import {
  defineComponent,
  reactive,
  computed,
  toRefs,
} from '@vue/composition-api';
import axios from '@/utils/axios';
import { getUsersInfoByIds } from '@/api/account';
import dayjs from 'dayjs';
// import AppError from '@/utils/error';
// @ts-ignore
import Intersect from 'vue-intersect';
import BaseUser from '@/components/base/User.vue';
import changelog from '@/components/changelog/index.vue';
import empty from '@/components/error/index.vue';
import {
  OperateList, DayOperateList, User, Obj,
} from './type';

export const getOperateList = (params: {
  relateId: string;
  pageNumber: number;
  pageSize: number;
}) => {
  return axios({
    method: 'get',
    url: '/msgcenter/operate/list',
    params,
  });
};

const getUsersObj = async (records: OperateList) => {
  const ids = [...new Set(records.map((item) => item.creator))];
  const users: User[] = (await getUsersInfoByIds(ids))?.data || [];
  const usersObj = users.reduce((obj: Obj, cur: User) => {
    obj[cur.id] = cur;
    return obj;
  }, {});
  return usersObj;
};

const handleOperateList = async (
  operateList: OperateList,
  dayOperateList: DayOperateList = [],
) => {
  const usersObj = await getUsersObj(operateList);
  operateList.forEach((operate) => {
    operate.user = usersObj[operate.creator] || {
      id: operate.creator,
      icon: '',
      name: '',
    };
    const day = dayjs(operate.gmtCreateTime).format('YYYY/MM/DD');
    const dayOperate = dayOperateList.find((item) => item.day === day);
    if (dayOperate) {
      dayOperate.operateList.push(operate);
    } else {
      dayOperateList.push({
        day,
        operateList: [operate],
      });
    }
  });
  return dayOperateList;
};

export default defineComponent({
  name: 'MaterialOperateHistory',
  inheritAttrs: false,
  components: {
    Intersect,
    BaseUser,
    changelog,
    empty,
  },
  props: {
    originalId: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const pageSize = 10;

    const defaultState = {
      dayOperateList: [] as DayOperateList,
      loading: false,
      pageLoading: false,
      pageNumber: 1,
      hasMore: false,
    };

    const state = reactive(defaultState);

    const showLoadMore = computed(() => state.hasMore && state.pageNumber > 1);

    const resetLoading = () => {
      state.loading = false;
      state.pageLoading = false;
    };

    const setDayOperateList = async (isLoadMore: boolean = false) => {
      try {
        if (!isLoadMore) state.loading = true;
        if (isLoadMore) state.pageLoading = true;
        const { data } = await getOperateList({
          relateId: props.originalId,
          pageNumber: state.pageNumber,
          pageSize,
        });
        const records = data?.records || [];

        state.dayOperateList = await handleOperateList(
          records,
          state.dayOperateList,
        );
        if (records.length === pageSize) {
          state.hasMore = true;
          state.pageNumber++;
        } else {
          state.hasMore = false;
        }
      } catch (error) {
        // throw new AppError(error);
      } finally {
        resetLoading();
      }
    };

    const loadMore = async () => {
      if (!showLoadMore) return;
      setDayOperateList(true);
    };

    setDayOperateList();

    return {
      ...toRefs(state),
      loadMore,
      showLoadMore,
    };
  },
});
