import axios from '@/utils/axios';
import { defineComponent, ref } from '@vue/composition-api';

type Tag = {
  id: string;
  tag: string;
};

const tagsEditor = defineComponent({
  props: {
    tags: {
      type: Array as () => Tag[],
      default: () => [],
    },
    editable: {
      type: Boolean,
      default: true,
      required: false,
    },
    shareId: {
      type: String,
      required: true,
    },
    type: {
      type: String,
      default: '1',
    },
    relateType: {
      type: String,
      default: '1',
    },
  },
  render() {
    const s = this as InstanceType<typeof tagsEditor>;
    return <div class="flex" style={{
      flexWrap: 'wrap',
    }}>
      {
        s.tags.map(tagItem => <div style={{
          border: '1px solid #dbdbdb',
          marginRight: '4px',
          fontSize: '12px',
          lineHeight: '18px',
          padding: '3px 12px',
          borderRadius: '12px',
          marginBottom: '4px',
          color: '#666',
        }}>
          <span>{tagItem.tag}</span>

          <i vShow={s.editable} class='el-icon-close' style={{
            cursor: 'pointer',
            marginLeft: '4px',
          }} onclick={() => { s.handleDeleteTag(tagItem); }}></i>
        </div>)
      }
      <div class='flex-center' vShow={s.editable && !s.useLabel} style={{
        width: '24px',
        height: '24px',
        border: '1px solid #dbdbdb',
        cursor: 'pointer',
        borderRadius: '12px',
      }} onclick={() => { s.useLabel = true; }}>
        <i class='el-icon-plus'></i>
      </div>

      <el-input vShow={s.editable && s.useLabel} vModel={s.label}
      size='mini' onblur={s.handleAddTag} style={{
        width: '200px',
      }}></el-input>
    </div>;
  },
  setup(props) {
    const label = ref('');
    const useLabel = ref(false);
    async function deleteTag(data: string[]) {
      await axios({
        url: '/bff-center/showCommon/deleteTag',
        method: 'POST',
        data,
      });
    }
    async function addTag(tags: string[]) {
      const { shareId, type, relateType } = props;
      return (await axios({
        url: '/bff-center/showCommon/addTags',
        method: 'POST',
        data: {
          shareId,
          type,
          relateType,
          tags,
        },
      })).data;
    }
    async function handleAddTag() {
      const res = await addTag([label.value]) as Tag[];
      props.tags.push(...res);
      label.value = '';
      useLabel.value = false;
    }
    async function handleDeleteTag(tag: Tag) {
      await deleteTag([tag.id]);
      props.tags.splice(props.tags.indexOf(tag), 1);
    }

    return {
      label,
      useLabel,

      handleAddTag,
      handleDeleteTag,
    };
  },
});
export default tagsEditor;
