





























































































import { i18n } from '@/i18n';
import { defineComponent, computed } from '@vue/composition-api';
import {
  getFileRelateIdByCatalogId,
} from '@/loaders/catalog';
import { openSingleton } from '@/utils/open-window';
import { isQt } from '@/utils/is-qt';
import { exec } from '@/utils/exec';
// import ModelInfo from '@/views/model/info';
// import MaterialInfo from '@/views/material/info';
import { useEl } from '_/composables/use-el';
import Catalog, { CatalogModel, CatalogModalTag } from '@/models/Catalog';
import ClothInfo from '@/views/test/ClothesDetail.vue';
import ModelInfo from '@/views/model/info/index.vue';
import HandbagInfo from '@/views/model/bag-info/index.vue';
import { getContextUser } from '@/loaders/context';
import { getModelCTMList } from '@/views/up-meeting/utils/utils';
import openQtWin from './open-qt-win';


function rpx(x: number, size: number = 1) {
  return `${(x * size)}px`;
  // return `${($size / $baseFontSize)}em`;
}

function isShoe(catalog: Catalog): catalog is CatalogModel {
  return catalog.relateType === 0 && !!catalog.fileInfo;
}
function isHandbag(catalog: Catalog): catalog is CatalogModel {
  return catalog.relateType === 3 && !!catalog.fileInfo;
}
function isClothing(catalog: Catalog): catalog is CatalogModel {
  return catalog.relateType === 4;
}

const checkHasModel = async (catalog: Catalog) => {
  const modelId = catalog.relateId;
  if (!modelId) return false;
  const ctmList = (await getModelCTMList(modelId, isHandbag(catalog) ? 'HANDBAG' : 'SHOE')).data;
  return !!(ctmList && ctmList.length);
};


const ItemFile = defineComponent({
  name: 'CatalogItemFile',
  props: {
    item: {
      required: true,
      type: Object as () => CatalogModel,
    },
    itemSize: {
      default: () => ({
        defaultWidth: 220,
        scale: 1,
      }),
      type: Object,
    },
    shownInfo: {
      type: Boolean,
      default: false,
    },
    tags: {
      type: Array as () => CatalogModalTag[],
    },
  },
  setup(p, { emit, root }) {
    const relateType = computed(() => p.item.relateType);
    const showTag = computed(() => p.tags && p.tags?.length !== 0);
    const tagContent = computed(() => {
      const firstTag = p.tags && p.tags[0];
      return i18n.t(`virtual-meetup.public.tags.${firstTag?.content}`);
    });

    const elemStyles = computed(() => {
      const { itemSize } = p;
      return {
        img: {
          width: rpx(itemSize.defaultWidth, itemSize.scale),
          height: rpx(152, itemSize.scale),
        },
        // img: {
        //   // height: rpx(152, itemSize.scale),
        // },
        infoContainer: {
          width: rpx(220, itemSize.scale),

          // fontSize: rpx(14, 1),
          // height: rpx(41, 1),
          // padding: `${rpx(10, 1)} ${rpx(10, 1)}`,
        },
      };
    });

    // 缩略图路径
    const imageSrc = computed(() => {
      const _fileInfo = p.item.fileInfo;
      return _fileInfo?.thumbnail ?? null;
    });
    // 默认展示图
    const defaultImageSrc = computed(() => {
      return require('@/assets/img/file-list/shoe-default.png');
    });
    // 是否显示下载
    const showDownload = computed(() => {
      // return true;
      const modelFileInfo = p.item.fileInfo ? p.item.fileInfo : null;
      return isQt() && modelFileInfo && modelFileInfo.modelType === 1
        && (p.item.hostData!.downloadState === 3
          || p.item.hostData!.downloadState === 4
          || p.item.hostData!.downloadState === 5);
    });
    // 是否显示info栏
    const shownInfo_ = computed(() => {
      return p.shownInfo ?? showDownload.value;
    });


    const el = useEl();
    const user = computed(() => {
      return getContextUser();
    });

    // 查看模型详情
    async function handleShowItemDetail(item: CatalogModel) {
      const { relateType: _relateType, id } = item;

      // 接收弹窗关闭信息
      let dialogResult;
      let relateId: string | undefined;
      if (_relateType !== undefined) {
        relateId = await getFileRelateIdByCatalogId({
          catalogId: id,
          workspaceId: item.workspaceId,
          relateType: _relateType,
        });
      } else {
        ({ relateId } = p.item);
      }


      if (el.value) {
        el.value.blur();
      }

      if (isShoe(item)) {
        // 客户端弹窗逻辑
        if (isQt()) {
          const url = `/qt/dialog/model/info?modelId=${relateId}`;
          return openQtWin({ url, query: { language: i18n.language } });
        }
        // 鞋款
        dialogResult = await root.$injectDialog({
          is: ModelInfo,
          props: {
            modelId: relateId,
            groupAppId: item.workspaceId,
          },
        });
      } else if (isHandbag(item)) {
        if (isQt()) {
          const url = `/qt/dialog/handbag/info?modelId=${relateId}`;
          return openQtWin({ url, query: { language: i18n.language } });
        }
        // 包包
        dialogResult = await root.$injectDialog({
          is: HandbagInfo,
          props: {
            modelId: relateId,
            groupAppId: item.workspaceId,
          },
        });
        // 客户端弹窗逻辑
      } else if (isClothing(item)) {
        // if (isQt()) {
        //   const url = `/qt/dialog/cloth/info?modelId=${relateId}`;
        //   return openQtWin(url);
        // }
        // 服装
        dialogResult = await root.$injectDialog({
          is: ClothInfo,
          props: {
            clothingId: relateId,
            isDialog: true,
          },
        });
      }
      return dialogResult;
    }

    // 处理双击事件
    async function handleDblClick() {
      if (!user.value || user.value.businessType === 1) {
        return;
      }
      const catalog = p.item;
      if (catalog.entryAppType === 26) {
        // 会议库检查是否能跳转到渲染页
        const hasModel = await checkHasModel(catalog);
        if (!hasModel) {
          throw Error(i18n.t('virtual-meetup.public.no_4ddat_to_render'));
        }
      }

      const { workspaceId, id: catalogId } = p.item;
      const queryWorkspaceId = workspaceId || undefined;

      if (isQt()) {
        // 获取此刻最新的relateId，避免打开的是旧模型
        const modelId = await getFileRelateIdByCatalogId({
          catalogId,
          workspaceId,
          relateType: relateType.value as number,
        });
        catalog.relateId = modelId;
        catalog.fileInfo && (catalog.fileInfo.id = modelId);

        // 优先处理下载事件
        const downloadState = catalog.hostData?.downloadState!;

        if (catalog.fileInfo) {
          const { fileInfo: fileInfo1 } = catalog;
          if (fileInfo1.modelType === 1) {
            if (!downloadState) {
              await exec('openRenderer', catalog);
            } else if (downloadState === 3) {
              await exec('pauseDownloadFile', catalog);
            } else {
              await exec('downloadFile', catalog);
            }
          } else {
            await exec('openRenderer', catalog);
          }
        }
        return;
      }

      // 判断是否2d线稿
      if (catalog.fileInfo) {
        const { fileInfo: fileInfo1 } = catalog;
        if (fileInfo1.modelType === 1 || fileInfo1.modelType === 2) {
          await root.$alert(i18n.t('file-list.catalog.2d_base_tips'),
            i18n.t('file-list.catalog.tips_title'),
            {
              confirmButtonText: i18n.t('file-list.catalog.confirm'),
              type: 'warning',
            });
          return;
        }
      }

      // 包包或鞋款
      if (isShoe(p.item) || isHandbag(p.item)) {
        // 跳转到渲染页
        await openSingleton(async () => {
          // 获取此刻最新的relateId，避免打开的是旧模型
          const modelId = await getFileRelateIdByCatalogId({
            catalogId,
            workspaceId,
            relateType: relateType.value as number,
          });
          return {
            name: 'ModelItem',
            params: {
              modelId,
            },
            query: {
              workspaceId: queryWorkspaceId,
              catalogId,
              relateType: relateType.value as unknown as string,
            },
          };
        }, true, 'ModelItem');

        // 服装
      } else if (isClothing(p.item)) {
        // 跳转到服装渲染页
        await openSingleton(async () => {
          // 获取此刻最新的relateId，避免打开的是旧模型
          const modelId = await getFileRelateIdByCatalogId({
            catalogId,
            workspaceId,
            relateType: relateType.value as number,
          });
          return {
            name: 'ClothesItem',
            params: {
              modelId,
            },
            query: {
              workspaceId: queryWorkspaceId,
              catalogId,
              relateType: relateType.value as unknown as string,
            },
          };
        }, true, 'ClothesItem');
      }
    }

    // 重命名
    function handleRenameFolder() {
      emit('rename', p.item);
    }

    // 取消下载
    async function cancelDownload() {
      await exec('cancelDownloadFile', p.item);
    }

    // 暂停下载
    async function pauseDownload() {
      await exec('pauseDownloadFile', p.item);
    }

    // 下载
    async function download() {
      await exec('downloadFile', p.item);
    }


    return {
      defaultImageSrc,
      imageSrc,
      relateType,
      elemStyles,
      fileInfo: computed(() => p.item.fileInfo),
      showDownload,
      shownInfo_,
      handleDblClick,
      handleRenameFolder,
      handleShowItemDetail,
      cancelDownload,
      pauseDownload,
      download,
      showTag,
      tagContent,
    };
  },
});

export default ItemFile;
